import { FC, memo, useEffect } from "react";
import { Divider, MenuItem } from "@mui/material";
import { useForm } from "react-hook-form";

import { useFlowData } from "Contexts/ChatFlowDashboard";
import StandardCard from "Components/ChatFlowDashboard/FlowDrawer/SharedComponents/StandardCard/StandardCard";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import BaseContainer from "../../../SharedComponents/BaseContainer/BaseContainer";
import BaseWrapper from "../../../SharedComponents/BaseWrapper";
import MsgSettings from "../../../SharedComponents/MsgSettings/MsgSettings";
import { FlowMessageProps } from "../types";

import classes from "../Styles.module.css";

const Type108Message: FC<FlowMessageProps> = ({
  nodeInfo,
  nodeLabel,
  reloadDrawer,
  currentMsgIndex,
}) => {
  const { dataFields, OnChatMsgUpdate, chatMsgs } = useFlowData();
  const currentChatMsg = chatMsgs[currentMsgIndex];

  const { control, watch, reset, setValue } = useForm({
    defaultValues: currentChatMsg,
  });

  useEffect(() => {
    if (reloadDrawer) reset(currentChatMsg);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reloadDrawer]);

  const formValues = watch();
  const { condition } = formValues;

  useEffect(() => {
    OnChatMsgUpdate(formValues);
  }, [OnChatMsgUpdate, formValues]);

  return (
    <form className={classes.Container}>
      {condition && (
        <BaseContainer
          nodeInfo={nodeInfo}
          nodeLabel={nodeLabel}
          control={control}
          setValue={setValue}
        >
          <BaseWrapper>
            {condition.conditions?.map(({ style, removable }, i) =>
              removable ? (
                <StandardCard
                  key={i}
                  cardTitle={`Condition ${i + 1}`}
                  enableClose={false}
                  style={style}
                >
                  {/* Match Values */}
                  <FormTextField
                    control={control}
                    name={`condition.conditions[${i}].data_field`}
                    label="Match Values"
                    className="mb-3 mt-3"
                    select
                  >
                    <MenuItem value={""} disabled>
                      {"Select a Field"}
                    </MenuItem>
                    {[...dataFields].map(({ display_name, field_name }) => (
                      <MenuItem
                        key={field_name}
                        value={field_name}
                        selected={
                          condition.conditions[i].data_field === field_name
                        }
                      >
                        {display_name}
                      </MenuItem>
                    ))}
                  </FormTextField>
                </StandardCard>
              ) : (
                <StandardCard
                  key={i}
                  cardTitle={`Else Condition`}
                  enableClose={false}
                  style={style}
                >
                  <p>If parameter contains a value</p>
                </StandardCard>
              )
            )}
          </BaseWrapper>
          <Divider className="mt-3" />
          <BaseWrapper>
            <MsgSettings
              setValue={setValue}
              control={control}
              hide={{
                assign_rules: true,
                jump: true,
                is_first_msg: true,
                is_last_msg: true,
                resend_on_error: true,
                skip_on_back: true,
              }}
            />
          </BaseWrapper>
        </BaseContainer>
      )}
    </form>
  );
};

export default memo(Type108Message);
