import { ListItem, ListItemIcon, ListItemText } from "@mui/material";
import { FC } from "react";
import { BootstrapTooltip } from "SharedComponents/Tooltip";
import classes from "./NodeListItem.module.css";

interface NodeListItemProps {
  Icon: () => JSX.Element;
  description: string;
  code: number;
  label: string;
  onDragStart: (event: any, nodeType: any) => void;
}

const NodeListItem: FC<NodeListItemProps> = ({
  Icon,
  description,
  code,
  label,
  onDragStart,
}) => {
  return (
    <span className={classes.Container}>
      <BootstrapTooltip title={description} placement="left">
        <ListItem
          className={classes.SubListItem}
          button
          disablePadding
          onDragStart={(event) =>
            onDragStart(event, JSON.stringify({ node_code: code, label }))
          }
          draggable
        >
          <ListItemIcon className={classes.ListIcon}>
            <Icon />
          </ListItemIcon>
          <ListItemText className={classes.SubListItemText} primary={label} />
        </ListItem>
      </BootstrapTooltip>
    </span>
  );
};

export default NodeListItem;
