import clsx from "clsx";
import CustomDefaultNode from "Components/ChatFlowDashboard/FlowRenderer/CustomNodes/CustomDefaultNode/CustomDefaultNode";
import FlowMiniMap from "Components/ChatFlowDashboard/FlowRenderer/FlowMiniMap/FlowMiniMap";
import NavigationBar from "Components/ChatFlowDashboard/FlowRenderer/NavigationBar/NavigationBar";
import {
  useFlowData,
  useFlowDrawer,
  useFlowHistory,
  useFlowNodeServices,
  useFlowServices,
} from "Contexts/ChatFlowDashboard";
import { MouseEvent, useCallback, useEffect, useMemo, useRef } from "react";
import ReactFlow, {
  Background,
  Controls,
  useReactFlow,
} from "react-flow-renderer";
import { useKeyPress } from "Hooks/useKeyPress";

import classes from "./FlowRenderer.module.css";

const FlowRenderer = () => {
  const { undo, redo } = useFlowHistory();
  const { isDrawerOpen } = useFlowDrawer();
  const { copyNode, pasteNode, duplicateNode } = useFlowServices();

  const {
    edges,
    onEdgesChange,
    nodes,
    fetchChatFlowData,
    fetchAllDataFields,
    copiedChatMsgs,
    copiedNode,
    currentChatMsg,
    currentNode,
    refreshNode,
  } = useFlowData();

  const {
    dragOverFlow,
    onConnect,
    addFlowNode,
    onNodeClick,
    deselectNode,
    onEdgeUpdate,
    onEdgesDelete,
    onNodesDelete,
    onNodeDragEnd,
  } = useFlowNodeServices();

  const nodeTypes = useMemo(
    () => ({
      customDefault: CustomDefaultNode,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [refreshNode]
  );

  useEffect(() => {
    Promise.all([fetchChatFlowData(), fetchAllDataFields()]);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const { project } = useReactFlow();

  const reactFlowWrapper = useRef<HTMLDivElement>(null);

  const onDrop = useCallback(
    (event: any) => {
      event.preventDefault();
      if (!reactFlowWrapper.current) return;

      const reactFlowBounds = reactFlowWrapper.current.getBoundingClientRect();
      const payload = event.dataTransfer.getData("application/reactflow");
      // check if the dropped element is valid
      if (typeof payload === "undefined" || !payload) return;

      const position = project({
        x: event.clientX - reactFlowBounds.left,
        y: event.clientY - reactFlowBounds.top,
      });

      const { node_code, label } = JSON.parse(payload);
      addFlowNode(node_code, label, position);
    },
    [addFlowNode, project]
  );

  useKeyPress(
    ["d", "c", "v", "y", "z", "Control", "Meta"],
    (event: KeyboardEvent) => {
      const { key, ctrlKey, metaKey } = event;
      switch (key.toLowerCase()) {
        case "c":
          if (metaKey || ctrlKey) {
            event.preventDefault();
            if (!currentChatMsg || !currentNode) {
              return;
            }
            copyNode();
          }
          break;
        case "v":
          if (metaKey || ctrlKey) {
            event.preventDefault();
            if (!reactFlowWrapper.current) {
              return;
            }
            if (!copiedChatMsgs || !copiedNode) {
              return;
            }
            pasteNode();
          }
          break;
        case "d":
          if (metaKey || ctrlKey) {
            event.preventDefault();
            if (!currentChatMsg || !currentNode) {
              return;
            }
            duplicateNode();
          }
          break;
        case "y":
          if (metaKey || ctrlKey) {
            event.preventDefault();
            redo();
          }
          break;
        case "z":
          if (metaKey || ctrlKey) {
            event.preventDefault();
            undo();
            break;
          }
      }
    }
  );

  return (
    <div className={clsx(isDrawerOpen && classes.Container, "providerflow")}>
      <NavigationBar />
      <div
        className={clsx(classes.ReactFlow_Wrapper, "reactflow-wrapper")}
        ref={reactFlowWrapper}
      >
        <ReactFlow
          onDragOver={dragOverFlow}
          // panOnScroll={true}
          onLoad={() => {
            // fitView();
          }}
          defaultNodes={nodes}
          defaultEdges={edges}
          fitView
          onNodeDragStop={(e, node) => {
            onNodeDragEnd(node);
          }}
          onDrop={onDrop}
          nodesDraggable={true}
          nodeTypes={nodeTypes}
          // snapToGrid={true}
          onEdgeUpdate={onEdgeUpdate}
          onEdgesDelete={onEdgesDelete}
          // onNodesChange={(props) => onNodesChange(props)}
          onEdgesChange={(props) => onEdgesChange(props)}
          onConnect={onConnect}
          onNodeClick={onNodeClick}
          onEdgeClick={(event, edge) => {}}
          onPaneClick={(event: MouseEvent<Element, globalThis.MouseEvent>) => {
            // console.log(event, event.clientX, event.clientY);
            deselectNode();
          }}
          onNodesDelete={onNodesDelete}
        >
          <Background gap={30} size={3} style={{ opacity: 0.2 }} />
          <FlowMiniMap />
          <Controls />
        </ReactFlow>
      </div>
    </div>
  );
};

export default FlowRenderer;
