import React, { useEffect } from "react";
import { Typography } from "@mui/material";
import { Control, UseFormSetValue } from "react-hook-form";
import FormInputField from "SharedComponents/FormComponents/FormInputField";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import { useFlowData } from "Contexts/ChatFlowDashboard";

interface NodeTitleProps {
  nodeInfo: string;
  nodeLabel?: string;
  control: Control<ChatFlowMsg.ChatFlowMsgInterface, any>;
  setValue: UseFormSetValue<any>;
}

const NodeTitle: React.FC<NodeTitleProps> = ({
  nodeInfo,
  nodeLabel,
  control,
  setValue,
}) => {
  const { setRefreshNode, currentNode, toggleEditingData } = useFlowData();

  const { _getWatch } = control;

  const watchFields = _getWatch("title");

  const titleBlur = (event: any) => {
    event.target.value === "" && nodeLabel && setValue("title", nodeLabel);
    toggleEditingData(false);
  };

  useEffect(() => {
    if (currentNode) {
      currentNode.data.label = watchFields ? (
        <p>{watchFields}</p>
      ) : (
        currentNode.data.label
      );
      setRefreshNode((prevState) => (prevState += 1));
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [watchFields]);

  return (
    <>
      <div>
        <Typography variant="body1" gutterBottom>
          {nodeInfo}
        </Typography>
      </div>

      <div className="mt-4">
        <Typography variant="body1" gutterBottom component="h1">
          Node Title
        </Typography>

        <FormInputField
          control={control}
          name="title"
          type="text"
          placeholder="Name the node (optional)"
          onBlur={titleBlur}
          onFocus={() => toggleEditingData(true)}
        />
      </div>
    </>
  );
};

export default NodeTitle;
