import { FC, memo, useEffect } from "react";
import { Divider } from "@mui/material";
import { useForm } from "react-hook-form";

import { useFlowData } from "Contexts/ChatFlowDashboard";
import Header from "Components/ChatFlowDashboard/FlowDrawer/SharedComponents/Header/Header";
import { HeaderType } from "Shared/Enums/ChatFlowMsgEnum";
import BaseContainer from "../../../SharedComponents/BaseContainer/BaseContainer";
import BaseWrapper from "../../../SharedComponents/BaseWrapper";
import CaptureInput from "../../../SharedComponents/CaptureInput/CaptureInput";

import MessageFields from "../../../SharedComponents/MessageFields/MessageFields";
import MsgSettings from "../../../SharedComponents/MsgSettings/MsgSettings";
import APIMessage from "../../../SharedComponents/APIMessage/APIMessage";

import { FlowMessageProps } from "../types";
import classes from "../Styles.module.css";

const Type105Message: FC<FlowMessageProps> = ({
  nodeInfo,
  nodeLabel,
  reloadDrawer,
  currentMsgIndex,
}) => {
  const { OnChatMsgUpdate, chatMsgs } = useFlowData();

  const currentChatMsg = chatMsgs[currentMsgIndex];

  const { control, watch, reset, setValue } = useForm({
    defaultValues: currentChatMsg,
  });

  useEffect(() => {
    if (reloadDrawer) reset(currentChatMsg);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reloadDrawer]);

  const formValues = watch();

  useEffect(() => {
    OnChatMsgUpdate(formValues);
  }, [OnChatMsgUpdate, formValues]);

  const { header, msg_settings } = formValues;

  return (
    <form className={classes.Container}>
      <BaseContainer
        nodeInfo={nodeInfo}
        nodeLabel={nodeLabel}
        control={control}
        setValue={setValue}
      >
        <BaseWrapper>
          <Header
            setValue={setValue}
            control={control}
            header={header}
            headerLabel="Header Media"
            askProvider
            headerTypes={[
              {
                label: "Image",
                value: HeaderType.IMAGE,
              },
              {
                label: "Video",
                value: HeaderType.VIDEO,
              },
            ]}
          />
        </BaseWrapper>
        <BaseWrapper>
          <MessageFields
            fieldLabel="Pre Message"
            disable_primary={true}
            disable_error={true}
            disable_pre={false}
            control={control}
            setValue={setValue}
          />
        </BaseWrapper>
        <BaseWrapper>
          <APIMessage control={control} setValue={setValue} />
        </BaseWrapper>
        <BaseWrapper>
          <MessageFields
            fieldLabel="Post Message"
            disable_primary={true}
            disable_error={true}
            disable_post={false}
            control={control}
            setValue={setValue}
          />
        </BaseWrapper>
        <BaseWrapper>
          <MessageFields
            disable_primary={true}
            control={control}
            setValue={setValue}
          />
        </BaseWrapper>
        <Divider className="mt-3" />
        <BaseWrapper>
          <CaptureInput
            response_type={msg_settings.response_type}
            save_input={msg_settings.save_input}
            data_field={msg_settings.data_field}
            control={control}
            setValue={setValue}
          />
        </BaseWrapper>
        <BaseWrapper>
          <MsgSettings setValue={setValue} control={control} />
        </BaseWrapper>
      </BaseContainer>
    </form>
  );
};

export default memo(Type105Message);
