import { Checkbox, CheckboxProps, FormControlLabel } from "@mui/material";
import clsx from "clsx";
import { FC } from "react";
import { Control, useController, UseControllerProps } from "react-hook-form";

interface FormCheckBoxProps extends UseControllerProps {
  control: Control<any, any>;
  label?: string;
}

const FormCheckBox: FC<CheckboxProps & FormCheckBoxProps> = ({
  control,
  defaultValue,
  name,
  label,
  className,
  rules,
  ...props
}) => {
  const {
    field: { value, onChange, ref },
  } = useController({ defaultValue, name, control, rules });
  return (
    <div className={clsx("d-flex", className)}>
      <FormControlLabel
        control={
          <Checkbox
            inputRef={ref}
            checked={value}
            onChange={onChange}
            {...props}
          />
        }
        label={label}
        sx={{ fontSize: "14px", lineHeight: "130%" }}
      />
      {/* <Checkbox inputRef={ref} checked={value} onChange={onChange} {...props} />
      {label && (
        <label style={{ fontSize: "14px", lineHeight: "130%" }}>{label}</label>
      )} */}
    </div>
  );
};

export default FormCheckBox;
