import { Typography } from "@mui/material";
import {
  useFlowData,
  useFlowDialog,
  useFlowNodeServices,
} from "Contexts/ChatFlowDashboard";
import { FC } from "react";
import { Control, UseFormSetValue } from "react-hook-form";
import { NodeDataLabel } from "Shared/Enums/ChatFlowNodeEnum";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import FormCheckBox from "SharedComponents/FormComponents/FormCheckBox";

interface MsgSettingsProps {
  control: Control<ChatFlowMsg.ChatFlowMsgInterface, any>;
  setValue: UseFormSetValue<ChatFlowMsg.ChatFlowMsgInterface>;
  hide?: {
    save_input?: boolean;
    is_first_msg?: boolean;
    is_last_msg?: boolean;
    assign_rules?: boolean;
    skip_on_back?: boolean;
    disable_auto_reminder?: boolean;
    jump?: boolean;
    resend_on_error?: boolean;
  };
}

const warningMessageForFirstMessage = (
  <>
    It seems that another node is marked as the "start node". There can only be
    one start node in a flow.
    <br />
    <br />
    Do you wish to make this node as a new start node?
  </>
);

const MsgSettings: FC<MsgSettingsProps> = ({
  control,
  setValue,
  hide = {
    save_input: false,
    assign_rules: false,
    disable_auto_reminder: false,
    is_first_msg: false,
    is_last_msg: false,
    jump: false,
    resend_on_error: false,
    skip_on_back: false,
  },
}) => {
  const { openAlertDialog, openWarningDialog } = useFlowDialog();
  const { updateNodeLabel } = useFlowNodeServices();
  const { chatMsgs, setRefreshNode, nodes } = useFlowData();

  const isMessageExist = (
    messageAnnotation: "is_first_msg" | "is_last_msg"
  ) => {
    return chatMsgs.find((e) => e.msg_settings[messageAnnotation]);
  };

  const isNodeConnectionExist = () => {
    const currentChatMsg =
      control._getWatch() as ChatFlowMsg.ChatFlowMsgInterface;

    const existInArray = (arr?: any[]) => {
      if (arr && arr.length > 0) return true;
      return false;
    };

    if (currentChatMsg) {
      if (currentChatMsg.trigger_id) {
        return {
          message: (
            <>
              Please remove connections extending from this node to mark it as
              an end node. An end node can not have subsequent downwards
              connections.
            </>
          ),
        };
      } else if (existInArray(currentChatMsg.options)) {
        return {
          message: (
            <>
              Not Allowed!
              <br />
              <br />
              An end node can not have subsequent downwards connections.
            </>
          ),
        };
      } else if (
        currentChatMsg.condition &&
        existInArray(currentChatMsg.condition.conditions)
      ) {
        return {
          message: (
            <>
              Not Allowed!
              <br />
              <br />
              An end node can not have subsequent downwards connections.
            </>
          ),
        };
      }

      return { success: true };
    }
    return { success: true };
  };

  const onChangeFirstMsg = (checked: boolean) => {
    const key = `msg_settings.is_first_msg` as any;

    // const msg: any = undefined;
    const msg = isMessageExist("is_first_msg");

    if (msg) {
      openWarningDialog(warningMessageForFirstMessage, "WARNING", () => {
        msg.msg_settings.is_first_msg = false;

        // update current msg data
        setValue(key, checked);
        updateNodeLabel(NodeDataLabel.First);

        const element = nodes.find((e) => e.id === msg.id);
        if (element) element.data.nodeDataLabel = undefined;
      });
    } else {
      setValue(key, checked);
      updateNodeLabel(NodeDataLabel.First);
    }
  };

  const onChange = (
    checked: boolean,
    field: "is_first_msg" | "is_last_msg"
  ) => {
    const key = `msg_settings.${field}` as any;

    if (checked) {
      if (field === "is_first_msg") {
        onChangeFirstMsg(checked);
      } else if (field === "is_last_msg") {
        const { message } = isNodeConnectionExist();
        if (message) openAlertDialog(message);
        else {
          setValue(key, checked);
          updateNodeLabel(NodeDataLabel.Last);
        }
      }
    } else {
      setValue(key, checked);
      updateNodeLabel(undefined);
    }
    return setRefreshNode((prevState) => (prevState += 1));
  };

  console.log(hide.is_last_msg);

  return (
    <div>
      <Typography variant="body1" gutterBottom component="h1" className="mb-2">
        Additional Options
      </Typography>

      {!hide.save_input && (
        <FormCheckBox
          control={control}
          name="msg_settings.is_first_msg"
          label="Mark as start node, the conversation will start from this node."
          className="mt-2"
          onChange={(_e, checked) => onChange(checked, "is_first_msg")}
        />
      )}

      {!hide.is_last_msg && (
        <FormCheckBox
          control={control}
          name="msg_settings.is_last_msg"
          label="Mark as end node, the conversation will restart on user response."
          className="mt-2"
          onChange={(_e, checked) => onChange(checked, "is_last_msg")}
        />
      )}

      {!hide.assign_rules && (
        <FormCheckBox
          control={control}
          name="msg_settings.assign_rules"
          label="Assign enquiry at this step based on a preset rule. Define assignment rules."
          className="mt-2"
        />
      )}

      {!hide.skip_on_back && (
        <FormCheckBox
          control={control}
          name="msg_settings.skip_on_back"
          label="Skip this message while going reverse in chat flow through # response."
          className="mt-2"
        />
      )}
      {!hide.disable_auto_reminder && (
        <FormCheckBox
          control={control}
          name="msg_settings.disable_auto_reminder"
          label="Stop Auto-Reminder after this message"
          className="mt-2"
        />
      )}

      {!hide.resend_on_error && (
        <FormCheckBox
          control={control}
          name="msg_settings.resend_on_error"
          label="Re-send message on invalid response from user"
          className="mt-2"
        />
      )}

      {!hide.jump && (
        <FormCheckBox
          control={control}
          name="msg_settings.jump"
          label="Jump to the next message"
          className="mt-2"
        />
      )}
    </div>
  );
};

export default MsgSettings;
