import { FC, memo, useEffect } from "react";
import { Button, Divider, Typography } from "@mui/material";
import { useForm } from "react-hook-form";

import { useFlowData, useFlowServices } from "Contexts/ChatFlowDashboard";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import FormInputField from "SharedComponents/FormComponents/FormInputField";
import BaseContainer from "../../../SharedComponents/BaseContainer/BaseContainer";
import BaseWrapper from "../../../SharedComponents/BaseWrapper";
import CaptureInput from "../../../SharedComponents/CaptureInput/CaptureInput";
import Header from "../../../SharedComponents/Header/Header";
import MessageFields from "../../../SharedComponents/MessageFields/MessageFields";
import MsgSettings from "../../../SharedComponents/MsgSettings/MsgSettings";
import SectionCard from "../../../SharedComponents/SectionCard/SectionCard";
import { FlowMessageProps } from "../types";

import classes from "../Styles.module.css";

import { IOSSwitch } from "SharedComponents/Switch";
import { OptionType } from "Shared/Enums/OptionType";

const Type111Message: FC<FlowMessageProps> = ({
  nodeInfo,
  reloadDrawer,
  currentMsgIndex,
}) => {
  const { OnChatMsgUpdate, chatMsgs, toggleEditingData } = useFlowData();
  const { addOption, removeOption } = useFlowServices();
  const currentChatMsg = chatMsgs[currentMsgIndex];
  const { control, watch, reset, setValue } = useForm({
    defaultValues: currentChatMsg,
  });
  const formValues = watch();
  const { header, post_msg, action, msg_settings } = formValues;
  const handleFooter = (
    _event: React.ChangeEvent<HTMLInputElement>,
    checked: boolean
  ) => setValue("post_msg", checked ? "" : undefined);

  useEffect(() => {
    if (reloadDrawer) reset(currentChatMsg);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reloadDrawer]);

  useEffect(() => {
    OnChatMsgUpdate(formValues);
  }, [OnChatMsgUpdate, formValues]);

  return (
    <form className={classes.Container}>
      <BaseContainer nodeInfo={nodeInfo} control={control} setValue={setValue}>
        <BaseWrapper>
          <Header
            setValue={setValue}
            control={control}
            header={header}
            headerTypes={[
              { value: "image", label: "Image" },
              { value: "video", label: "Video" },
              { value: "text", label: "Text" },
              { value: "file", label: "Document" },
            ]}
          />
        </BaseWrapper>
        <BaseWrapper>
          <MessageFields control={control} setValue={setValue} />
        </BaseWrapper>
        <BaseWrapper>
          <div className="d-flex justify-content-between align-items-center mb-2">
            <Typography variant="body1" gutterBottom component="h1">
              Footer
            </Typography>
            <IOSSwitch
              checked={post_msg !== undefined}
              onChange={handleFooter}
            />
          </div>
          {post_msg !== undefined && (
            <FormTextField
              className="mt-3"
              label="Footer text"
              name={`post_msg`}
              control={control}
              onFocus={() => toggleEditingData(true)}
              onBlur={() => toggleEditingData(false)}
            />
          )}
        </BaseWrapper>
        <BaseWrapper>
          <div className="d-flex justify-content-between align-items-center mb-2">
            <Typography variant="body1" gutterBottom component="h1">
              Section Button Label
            </Typography>
          </div>
          <FormInputField
            className="mt-1"
            placeholder="Button Label"
            name={`action.button`}
            control={control}
            onFocus={() => toggleEditingData(true)}
            onBlur={() => toggleEditingData(false)}
          />
        </BaseWrapper>
        <BaseWrapper>
          <div className="d-flex justify-content-between align-items-center mb-2">
            <Typography variant="body1" gutterBottom component="h1">
              List Section
            </Typography>
            {action && (
              <Button
                onClick={() => {
                  addOption({
                    type: OptionType.LIST_SECTION,
                    makeHandle: false,
                  });
                }}
                className={classes.CustomButton}
                disabled={
                  action && action.sections
                    ? action.sections?.length >= 10
                    : false
                }
              >
                Add a Section
              </Button>
            )}
          </div>
          {action &&
            action.sections?.map((section, i) => (
              <SectionCard
                key={i}
                cardTitle={`Section ${i + 1}`}
                closeCard={() =>
                  removeOption({
                    type: OptionType.LIST_SECTION,
                    index: i,
                    id: section.id,
                    removeHandle: true,
                  })
                }
              >
                {/* Title */}
                <FormTextField
                  control={control}
                  name={`action.sections[${i}].title`}
                  label="Title"
                  className="mb-3 mt-3"
                  onFocus={() => toggleEditingData(true)}
                  onBlur={() => toggleEditingData(false)}
                />
                <Button
                  onClick={() => {
                    addOption({
                      type: OptionType.LIST_SECTION_ITEM,
                      index: i,
                      makeHandle: true,
                    });
                  }}
                  className={classes.CustomButton}
                  sx={{ mb: 2 }}
                >
                  Add a List Item
                </Button>
                {section.rows.map((row, j) => (
                  <SectionCard
                    key={j}
                    cardTitle={`List Item ${j + 1}`}
                    closeCard={() =>
                      removeOption({
                        type: OptionType.LIST_SECTION_ITEM,
                        index: i,
                        id: row.id,
                        removeHandle: true,
                      })
                    }
                    style={{
                      ...row.style,
                      borderRadius: 0,
                      paddingBottom: "24px",
                    }}
                  >
                    <FormTextField
                      control={control}
                      className="mb-4"
                      label="List Title"
                      name={`action.sections[${i}].rows[${j}].title`}
                      inputProps={{ maxLength: 24 }}
                      onFocus={() => toggleEditingData(true)}
                      onBlur={() => toggleEditingData(false)}
                    />

                    <FormTextField
                      control={control}
                      label="List Description"
                      name={`action.sections[${i}].rows[${j}].description`}
                      inputProps={{ maxLength: 72 }}
                      onFocus={() => toggleEditingData(true)}
                      onBlur={() => toggleEditingData(false)}
                    />
                  </SectionCard>
                ))}
              </SectionCard>
            ))}
        </BaseWrapper>
        <Divider className="mt-3" />
        <BaseWrapper>
          <CaptureInput
            response_type={msg_settings.response_type}
            save_input={msg_settings.save_input}
            data_field={msg_settings.data_field}
            control={control}
            setValue={setValue}
          />
        </BaseWrapper>
        <BaseWrapper>
          <MsgSettings
            setValue={setValue}
            control={control}
            hide={{ jump: true, is_last_msg: true }}
          />
        </BaseWrapper>
      </BaseContainer>
    </form>
  );
};

export default memo(Type111Message);
