import { FC, memo, useEffect } from "react";
import { Button, Divider, MenuItem, Typography } from "@mui/material";
import { useForm } from "react-hook-form";

import FormTextField from "SharedComponents/FormComponents/FormTextField";
import { IF_ELSE_TYPES } from "Shared/Enums/ChatFlowMsgEnum";
import { OptionType } from "Shared/Enums/OptionType";
import StandardCard from "Components/ChatFlowDashboard/FlowDrawer/SharedComponents/StandardCard/StandardCard";
import { useFlowData, useFlowServices } from "Contexts/ChatFlowDashboard";
import BaseContainer from "../../../SharedComponents/BaseContainer/BaseContainer";
import BaseWrapper from "../../../SharedComponents/BaseWrapper";
import MsgSettings from "../../../SharedComponents/MsgSettings/MsgSettings";
import { FlowMessageProps } from "../types";

import classes from "../Styles.module.css";

const Type109Message: FC<FlowMessageProps> = ({
  nodeInfo,
  nodeLabel,
  reloadDrawer,
  currentMsgIndex,
}) => {
  const { dataFields, OnChatMsgUpdate, chatMsgs, toggleEditingData } =
    useFlowData();
  const { addOption, removeOption } = useFlowServices();

  const currentChatMsg = chatMsgs[currentMsgIndex];

  const { control, watch, reset, setValue } = useForm({
    defaultValues: currentChatMsg,
  });
  const formValues = watch();
  const { condition } = formValues;

  useEffect(() => {
    if (reloadDrawer) reset(currentChatMsg);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reloadDrawer]);

  useEffect(() => {
    OnChatMsgUpdate(formValues);
  }, [OnChatMsgUpdate, formValues]);

  return (
    <form className={classes.Container}>
      {condition && (
        <BaseContainer
          nodeInfo={nodeInfo}
          nodeLabel={nodeLabel}
          control={control}
          setValue={setValue}
        >
          <BaseWrapper>
            <FormTextField
              control={control}
              name="condition.if_else_param_type"
              label={"Select Type"}
              className="mb-2 w-100"
              select
            >
              {[
                {
                  label: "Match with Lead Data Field",
                  value: IF_ELSE_TYPES.DATA_FIELD,
                },
                {
                  label: "Match with User Response",
                  value: IF_ELSE_TYPES.USER_RESPONSE,
                },
              ].map(({ label, value }) => (
                <MenuItem
                  key={value}
                  value={value}
                  selected={condition.if_else_param_type === value}
                >
                  {label}
                </MenuItem>
              ))}
            </FormTextField>
          </BaseWrapper>
          {condition.if_else_param_type === IF_ELSE_TYPES.DATA_FIELD && (
            <BaseWrapper>
              <FormTextField
                control={control}
                name="condition.evaluate_data_field"
                label={"Select Data Field"}
                className="mb-2 w-100"
                select
              >
                <MenuItem value={""} disabled>
                  {"Select a Field"}
                </MenuItem>
                {[...dataFields].map(({ display_name, field_name }) => (
                  <MenuItem
                    key={field_name}
                    value={field_name}
                    selected={condition.evaluate_data_field === field_name}
                  >
                    {display_name}
                  </MenuItem>
                ))}
              </FormTextField>
            </BaseWrapper>
          )}
          <BaseWrapper>
            <div className="d-flex justify-content-between align-items-center mb-2">
              <Typography variant="body1" gutterBottom component="h1">
                Conditions
              </Typography>
              <Button
                onClick={() => {
                  addOption({
                    type: OptionType.CONDITION,
                    makeHandle: true,
                  });
                }}
                className={classes.CustomButton}
                disabled={
                  condition.conditions
                    ? condition.conditions?.length >= 14
                    : false
                }
              >
                Add New Condition
              </Button>
            </div>
            {condition.conditions?.map(
              (
                { id, values, trigger_id, style, removable, validation_type },
                i
              ) =>
                removable ? (
                  <StandardCard
                    key={i}
                    cardTitle={`Condition ${i + 1}`}
                    closeCard={() =>
                      removeOption({
                        type: OptionType.CONDITION,
                        index: i,
                        id: id,
                        removeHandle: removable,
                      })
                    }
                    style={style}
                  >
                    {/* Match Values */}
                    <FormTextField
                      control={control}
                      name={`condition.conditions[${i}].values`}
                      label="Match Values"
                      className="mb-3 mt-3"
                      onFocus={() => toggleEditingData(true)}
                      onBlur={() => toggleEditingData(false)}
                    />
                  </StandardCard>
                ) : null
            )}
            <div className={classes.box}>
              <h1>Else Condition</h1>
              <p>Any non-matching value</p>
            </div>
          </BaseWrapper>
          <Divider className="mt-3" />
          <BaseWrapper>
            <MsgSettings
              setValue={setValue}
              control={control}
              hide={{
                assign_rules: true,
                jump: true,
                is_first_msg: true,
                is_last_msg: true,
                resend_on_error: true,
                skip_on_back: true,
              }}
            />
          </BaseWrapper>
        </BaseContainer>
      )}
    </form>
  );
};

export default memo(Type109Message);
