import React, { useCallback, useEffect, useState } from "react";
import { GoogleApiWrapper, Map, Marker } from "google-maps-react";
import classes from "./MapContainer.module.css";
import clsx from "clsx";
import "./Map.css";
import { InputBase, Paper, IconButton, Box } from "@mui/material";
import { Search } from "@mui/icons-material";
import PlaceRecommendationList from "./PlaceRecommendationList/PlaceRecommendationList";

const MapContainer = ({ google, onChange, initialCenter }) => {
  const [currentLocation, setCurrentLocation] = useState({
    lat: Number(initialCenter.lat) || 20.5937,
    lng: Number(initialCenter.lng) || 78.9629,
  });

  const [isOpenRecommendation, openRecommendation] = useState(false);

  const mapRef = React.useRef();
  const inputRef = React.useRef();

  const [markers, setMarkers] = useState([
    {
      lat: Number(initialCenter.lat) || 20.5937,
      lng: Number(initialCenter.lat) || 78.9629,
    },
  ]);

  const [recommendations, addRecommendation] = useState([]);

  const recenterMap = useCallback(
    ({ lat, lng }) => {
      setMarkers((prevState) => {
        return [{ lat, lng }];
      });

      // eslint-disable-next-line no-unused-vars
      const maps = google.maps;

      setCurrentLocation({ lat, lng });
    },
    [google.maps]
  );

  useEffect(() => {
    recenterMap(initialCenter);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onMapClicked = useCallback((mapProps, map, { latLng }) => {
    const { lat, lng } = latLng;

    const geocoder = new google.maps.Geocoder();

    const latlng = { lat: lat(), lng: lng() };

    const service = new google.maps.places.PlacesService(map);

    geocoder
      .geocode({
        location: latlng,
      })
      .then((response) => {
        if (response.results[0]) {
          const request = {
            placeId: response.results[0].place_id,
            fields: ["name", "formatted_address", "place_id", "geometry"],
          };

          service.getDetails(request, (place, status) => {
            if (
              status === google.maps.places.PlacesServiceStatus.OK &&
              place &&
              place.geometry &&
              place.geometry.location
            ) {
              onChange({
                ...latlng,
                name: place.name,
                address: place.formatted_address,
              });
            }
          });
        }
      })
      .catch((e) => window.alert("Geocoder failed due to: " + e));

    setMarkers((prevState) => {
      return [{ lat: lat(), lng: lng() }];
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const search = useCallback(() => {
    if (currentLocation) {
      // Create the search box and link it to the UI element.
      new google.maps.places.SearchBox(inputRef.current);
    }
  }, [currentLocation, google.maps]);

  const displaySuggestions = function (predictions, status) {
    if (status !== google.maps.places.PlacesServiceStatus.OK || !predictions) {
      return addRecommendation([]);
    }

    return addRecommendation(predictions);
  };

  return (
    currentLocation && (
      <>
        <div
          style={{
            width: "100%",
            position: "relative",
            height: "300px",
            display: "flex",
          }}
        >
          <Box
            sx={{
              width: "100%",
              position: "relative",
              height: "100%",

              "& div[aria-roledescription='map']": {
                cursor: "crosshair !important",
              },
            }}
          >
            <Map
              ref={mapRef}
              google={google}
              zoom={15}
              initialCenter={currentLocation}
              centerAroundCurrentLocation={true}
              onClick={onMapClicked}
              onBoundsChanged={search}
              center={currentLocation}
              panControl={true}
              draggable
              mapTypeControl={false}
              fullscreenControl={false}
              zoomControl={true}
              streetViewControl={false}
            >
              {markers.map((marker) => {
                return (
                  <Marker
                    position={marker}
                    key={`${marker.lat}-${marker.lng}`}
                  />
                );
              })}
            </Map>
          </Box>

          <div className={clsx(classes.Search_Box)}>
            <div className={classes.Input_Container}>
              <Paper
                sx={{
                  p: "5px 10px",
                  display: "flex",
                  alignItems: "center",
                  width: "95%",
                  borderRadius: "8px",
                  margin: "2% auto",
                }}
              >
                <IconButton sx={{ p: "10px" }} aria-label="search">
                  <Search />
                </IconButton>
                <InputBase
                  onFocus={(e) => openRecommendation(true)}
                  // onBlurCapture={(e) => openRecommendation(false)}
                  onInput={({ target }) => {
                    // retrieves autocomplete predictions programmatically from the autocomplete service, and displays them as an HTML list.

                    const service =
                      new google.maps.places.AutocompleteService();

                    service.getPlacePredictions(
                      { input: target.value },
                      displaySuggestions
                    );
                  }}
                  inputRef={inputRef}
                  sx={{ ml: 1, flex: 1 }}
                  placeholder="Search your Location"
                  inputProps={{ "aria-label": "Search Your Location" }}
                />
              </Paper>

              {isOpenRecommendation && (
                <PlaceRecommendationList
                  isOpen={
                    isOpenRecommendation && inputRef.current?.value.length >= 1
                  }
                  recenterMap={recenterMap}
                  google={google}
                  predictions={recommendations}
                  onChange={onChange}
                  closeList={() => openRecommendation(false)}
                />
              )}
            </div>
          </div>
        </div>
      </>
    )
  );
};

export default GoogleApiWrapper({
  apiKey: process.env.REACT_APP_MAP,
})(MapContainer);
