import { Settings } from "@mui/icons-material";
import { IconButton } from "@mui/material";

import {
  CurrentDrawerModes,
  useFlowData,
  useFlowDrawer,
} from "Contexts/ChatFlowDashboard";

const FlowSettingButton = () => {
  const { flowSettingsLoaded } = useFlowData();
  const { setCurrentDrawerMode } = useFlowDrawer();

  return (
    <IconButton
      disabled={!flowSettingsLoaded}
      onClick={() =>
        setCurrentDrawerMode((currentDrawerMode: CurrentDrawerModes) =>
          currentDrawerMode === CurrentDrawerModes.MENU
            ? CurrentDrawerModes.SETTING
            : CurrentDrawerModes.MENU
        )
      }
    >
      <Settings />
    </IconButton>
  );
};

export default FlowSettingButton;
