import { Button, Divider, Typography } from "@mui/material";
import { useFlowData, useFlowServices } from "Contexts/ChatFlowDashboard";
import { OptionType } from "Shared/Enums/OptionType";
import { FC, memo, useEffect } from "react";
import { useForm } from "react-hook-form";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import BaseContainer from "../../../SharedComponents/BaseContainer/BaseContainer";
import BaseWrapper from "../../../SharedComponents/BaseWrapper";
import CaptureInput from "../../../SharedComponents/CaptureInput/CaptureInput";
import Header from "../../../SharedComponents/Header/Header";
import MessageFields from "../../../SharedComponents/MessageFields/MessageFields";
import MsgSettings from "../../../SharedComponents/MsgSettings/MsgSettings";
import StandardCard from "../../../SharedComponents/StandardCard/StandardCard";
import { FlowMessageProps } from "../types";
import classes from "../Styles.module.css";

const Type100Message: FC<FlowMessageProps> = ({
  nodeInfo,
  nodeLabel,
  reloadDrawer,
  currentMsgIndex,
}) => {
  const { OnChatMsgUpdate, chatMsgs, toggleEditingData } = useFlowData();
  const { addOption, removeOption } = useFlowServices();

  const currentChatMsg = chatMsgs[currentMsgIndex];

  const { control, watch, reset, setValue } = useForm({
    defaultValues: currentChatMsg,
  });

  useEffect(() => {
    if (reloadDrawer) reset(currentChatMsg);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reloadDrawer]);

  const formValues = watch();

  useEffect(() => {
    OnChatMsgUpdate(formValues);
  }, [OnChatMsgUpdate, formValues]);

  const { header, options, msg_settings } = formValues;

  return (
    <form className={classes.Container}>
      <BaseContainer
        nodeInfo={nodeInfo}
        nodeLabel={nodeLabel}
        control={control}
        setValue={setValue}
      >
        <BaseWrapper>
          <Header
            setValue={setValue}
            control={control}
            header={header}
            headerTypes={[
              { value: "image", label: "Image" },
              { value: "video", label: "Video" },
              { value: "file", label: "Document" },
            ]}
          />
        </BaseWrapper>

        <BaseWrapper>
          <MessageFields control={control} setValue={setValue} />
        </BaseWrapper>

        <BaseWrapper>
          <div className="d-flex justify-content-between align-items-center mb-2">
            <Typography variant="body1" gutterBottom component="h1">
              Define Options
            </Typography>

            <Button
              onClick={() => {
                addOption({ type: OptionType.OPTION });
              }}
              className={classes.CustomButton}
            >
              Add a New Option
            </Button>
          </div>

          {options &&
            options.length > 0 &&
            options.map((option, i) => {
              return (
                <StandardCard
                  key={i}
                  cardTitle={`Option ${i + 1}`}
                  closeCard={() =>
                    removeOption({
                      type: OptionType.OPTION,
                      index: i,
                      id: option.id,
                    })
                  }
                >
                  <FormTextField
                    control={control}
                    name={`options[${i}].label`}
                    label="Enter Label"
                    className="mb-3 mt-3"
                    onFocus={() => toggleEditingData(true)}
                    onBlur={() => toggleEditingData(false)}
                  />
                  <FormTextField
                    control={control}
                    name={`options[${i}].value`}
                    label="Enter Value"
                    onFocus={() => toggleEditingData(true)}
                    onBlur={() => toggleEditingData(false)}
                  />
                </StandardCard>
              );
            })}
        </BaseWrapper>

        <Divider className="mt-3" />

        <BaseWrapper>
          <CaptureInput
            response_type={msg_settings.response_type}
            save_input={msg_settings.save_input}
            data_field={msg_settings.data_field}
            control={control}
            setValue={setValue}
          />
        </BaseWrapper>

        <BaseWrapper>
          <MsgSettings setValue={setValue} control={control} />
        </BaseWrapper>
      </BaseContainer>
    </form>
  );
};

export default memo(Type100Message);
