import clsx from "clsx";
import { Button } from "@mui/material";
import classes from "./Styles.module.css";
import { FC } from "react";
import { ZoneProps } from ".";

const ImageZone: FC<ZoneProps> = ({ removeFile, file, fileUrl }) => {
  return (
    <>
      <div className="d-flex justify-content-center">
        <div className={classes.Upload_Overlay}>
          <Button
            className={clsx("mt-1", classes.RemoveButton)}
            onClick={removeFile}
          >
            Remove
          </Button>
        </div>
        <img
          src={fileUrl || file.preview}
          className={clsx(classes.image_preview, "w-100")}
          alt="preview"
        />
      </div>
    </>
  );
};

export default ImageZone;
