import { Divider, Drawer } from "@mui/material";
import {
  DrawerHeader,
  FlowMessageHandler,
  NodeList,
} from "Components/ChatFlowDashboard/FlowDrawer";
import { DRAWER_WIDTH } from "Constants/FlowDashbaord/FlowDrawer";
import {
  CurrentDrawerModes,
  useFlowDrawer,
  useFlowData,
} from "Contexts/ChatFlowDashboard";
import { memo } from "react";
import NodeService from "Services/ChatFlowService/NodeService";
import classes from "./FlowDrawer.module.css";
import FlowSettings from "Components/ChatFlowDashboard/FlowDrawer/FlowSettings/FlowSettings";

const FlowDrawer = memo(() => {
  const { isDrawerOpen, currentDrawerMode } = useFlowDrawer();
  const { currentChatMsg, currentMsgIndex, reloadDrawer } = useFlowData();

  const nodeLabel = () => {
    if (!currentChatMsg) return "Unknown Type";

    const node = NodeService.all_nodes_list.find(
      (type) => type.code === currentChatMsg.type
    );

    if (node) return node.label;
    return "Unknown Type";
  };

  return (
    <Drawer
      sx={{
        ":root": {
          maxWidth: 500,
        },
        flexShrink: 0,
        minWidth: 300,
        width: DRAWER_WIDTH,
        "& .MuiDrawer-paper": {
          width: "25%",
          borderLeft: "1px solid rgba(0, 0, 0, 0.12)",
        },
      }}
      variant="persistent"
      anchor="right"
      open={isDrawerOpen}
    >
      <DrawerHeader>
        <h1 className={classes.Header}>
          {currentDrawerMode === CurrentDrawerModes.NODE && currentChatMsg?.type
            ? nodeLabel()
            : currentDrawerMode === CurrentDrawerModes.SETTING
            ? "Flow Setting"
            : "Create a Node"}
        </h1>
      </DrawerHeader>
      <Divider />

      <div className={classes.Drawer}>
        {typeof currentMsgIndex === "number" &&
        currentChatMsg &&
        currentDrawerMode === CurrentDrawerModes.NODE ? (
          <FlowMessageHandler
            code={currentChatMsg.type}
            currentMsgIndex={currentMsgIndex}
            reloadDrawer={reloadDrawer}
          />
        ) : currentDrawerMode === CurrentDrawerModes.SETTING ? (
          <FlowSettings />
        ) : currentDrawerMode === CurrentDrawerModes.MENU ? (
          <NodeList />
        ) : (
          <NodeList />
        )}
      </div>
    </Drawer>
  );
});

export default FlowDrawer;
