import { FC, Fragment, memo, useEffect } from "react";
import { Divider, MenuItem, Typography } from "@mui/material";
import { useForm } from "react-hook-form";

import { useFlowData } from "Contexts/ChatFlowDashboard";
import BaseContainer from "../../../SharedComponents/BaseContainer/BaseContainer";
import BaseWrapper from "../../../SharedComponents/BaseWrapper";
import MsgSettings from "../../../SharedComponents/MsgSettings/MsgSettings";
import MapContainer from "../../../SharedComponents/MapContainer/MapContainer";
import { LocationType } from "Shared/Enums/ChatFlowMsgEnum";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import FormInputField from "SharedComponents/FormComponents/FormInputField";
import { FlowMessageProps } from "../types";

import classes from "../Styles.module.css";

const Type103Message: FC<FlowMessageProps> = ({
  nodeInfo,
  nodeLabel,
  reloadDrawer,
  currentMsgIndex,
}) => {
  const { OnChatMsgUpdate, chatMsgs, toggleEditingData } = useFlowData();

  const currentChatMsg = chatMsgs[currentMsgIndex];

  const { control, watch, reset, setValue } = useForm({
    defaultValues: currentChatMsg,
  });

  useEffect(() => {
    if (reloadDrawer) reset(currentChatMsg);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reloadDrawer]);

  const formValues = watch();

  const updatePlace = (place: any) => {
    setValue("location_msg.lat", place.lat);
    setValue("location_msg.lng", place.lng);
    setValue("location_msg.name", place.name);
    setValue("location_msg.address", place.address);
  };

  useEffect(() => {
    OnChatMsgUpdate(formValues);
  }, [OnChatMsgUpdate, formValues]);

  const { location_msg } = formValues;
  const noCycler = {
    sx: {
      MozAppearance: "textfield",

      "&::-webkit-outer-spin-button,&::-webkit-inner-spin-button": {
        WebkitAppearance: "none",
        margin: 0,
      },
    },
  };

  return (
    <form className={classes.Container}>
      <BaseContainer
        nodeInfo={nodeInfo}
        nodeLabel={nodeLabel}
        control={control}
        setValue={setValue}
      >
        <BaseWrapper>
          <BaseWrapper>
            <FormTextField
              select
              control={control}
              label="Select Location Type"
              name="location_msg.type"
            >
              {[
                { label: "Select on Map", value: LocationType.MAP },
                { label: "Enter Coordinates", value: LocationType.MANUAL },
                { label: "Enter API url", value: LocationType.API },
              ].map(({ label, value }, i) => (
                <MenuItem key={i} value={value}>
                  {label}
                </MenuItem>
              ))}
            </FormTextField>
          </BaseWrapper>
        </BaseWrapper>
        {location_msg && (
          <Fragment>
            {location_msg.type === LocationType.MAP && (
              <BaseWrapper>
                <MapContainer
                  onChange={updatePlace}
                  initialCenter={{
                    lat: location_msg.lat ?? 20.5937,
                    lng: location_msg.lng ?? 78.9629,
                  }}
                />
              </BaseWrapper>
            )}
            {location_msg.type === LocationType.MANUAL && (
              <BaseWrapper>
                <FormInputField
                  control={control}
                  name={`location_msg.lat`}
                  type="number"
                  placeholder="Latitude"
                  inputProps={noCycler}
                  onFocus={() => toggleEditingData(true)}
                  onBlur={() => toggleEditingData(false)}
                />
              </BaseWrapper>
            )}
            {location_msg.type === LocationType.MANUAL && (
              <BaseWrapper>
                <FormInputField
                  control={control}
                  name={`location_msg.lng`}
                  type="number"
                  placeholder="Longitude"
                  inputProps={noCycler}
                  onFocus={() => toggleEditingData(true)}
                  onBlur={() => toggleEditingData(false)}
                />
              </BaseWrapper>
            )}
            {/* Location Details */}
            {(location_msg.type === LocationType.MANUAL ||
              location_msg.type === LocationType.MAP) && (
              <BaseWrapper>
                <Typography variant="body1" gutterBottom component="h1">
                  Location Details
                </Typography>
                {/* location name */}
                <BaseWrapper>
                  <FormInputField
                    control={control}
                    name={`location_msg.name`}
                    type="text"
                    placeholder="Name the Location"
                    onFocus={() => toggleEditingData(true)}
                    onBlur={() => toggleEditingData(false)}
                  />
                </BaseWrapper>
                {/* location address */}
                <BaseWrapper>
                  <FormInputField
                    control={control}
                    name={`location_msg.address`}
                    type="text"
                    placeholder="Address"
                    onFocus={() => toggleEditingData(true)}
                    onBlur={() => toggleEditingData(false)}
                  />
                </BaseWrapper>
              </BaseWrapper>
            )}
            {location_msg.type === LocationType.API && (
              <div className="mt-3">
                <Typography
                  className="mb-2"
                  variant="body1"
                  gutterBottom
                  component="h1"
                >
                  Location Details
                </Typography>

                <FormInputField
                  control={control}
                  name={`location_msg.api_url`}
                  type="text"
                  placeholder="Enter your API url"
                  onFocus={() => toggleEditingData(true)}
                  onBlur={() => toggleEditingData(false)}
                />
              </div>
            )}
          </Fragment>
        )}
        <BaseWrapper>
          <Typography variant="body1" gutterBottom component="h1">
            Error Message
          </Typography>
          <FormTextField
            rows={5}
            multiline
            placeholder="Error Message"
            control={control}
            name="error_msg"
            onFocus={() => toggleEditingData(true)}
            onBlur={() => toggleEditingData(false)}
          />
        </BaseWrapper>
        <Divider className="mt-3" />
        <BaseWrapper>
          <MsgSettings
            setValue={setValue}
            control={control}
            hide={{ save_input: true }}
          />
        </BaseWrapper>
      </BaseContainer>
    </form>
  );
};

export default memo(Type103Message);
