import { FC } from "react";
import { Box } from "@mui/material";

import FlowSettingButton from "./FlowSettingButton";

const FlowControls: FC = () => {
  return (
    <Box
      position="absolute"
      bottom="5%"
      right="calc(25% + 20px)"
      zIndex="appBar"
    >
      <FlowSettingButton />
    </Box>
  );
};

export default FlowControls;
