import { FC, Fragment } from "react";
import { Button, MenuItem, Typography } from "@mui/material";
import { useParams } from "react-router-dom";
import { useForm } from "react-hook-form";

import FormTextField from "SharedComponents/FormComponents/FormTextField";
import FormIOSSwitch from "SharedComponents/FormComponents/FormIOSSwitch";
import { ChatFlowSettingInterface } from "Shared/Interfaces/ChatFlowSettingInterface";
import { useSnacks } from "Contexts/Snack";
import { useFlowData } from "Contexts/ChatFlowDashboard";
import { secondsToHms } from "Utils";

import BaseWrapper from "../SharedComponents/BaseWrapper";
import Header from "../SharedComponents/Header/Header";
import StandardCard from "../SharedComponents/StandardCard/StandardCard";

import classes from "./FlowSettings.module.css";
import ChatFlowApi from "Apis/ChatFlowApi";

const FlowSettings: FC = () => {
  const { chatflow_id } = useParams<{ chatflow_id: string }>();
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { flowSettings, setReloadDrawer, setFlowSettings, toggleEditingData } =
    useFlowData();
  const { control, watch, handleSubmit, setValue } = useForm({
    defaultValues: flowSettings,
  });
  const formValues = watch();

  const onSubmit = async (data: ChatFlowSettingInterface) => {
    try {
      if (chatflow_id) {
        // update
        const res = await ChatFlowApi.putChatFlowSettings(chatflow_id, data);

        if (!res.success) {
          throw Error(res.message);
        }
        setSuccessMessage(res.message);
        setFlowSettings(data);
      }
    } catch (error: any) {
      setErrorMessage(error.message);
    }
  };

  return (
    <form className={classes.Container} onSubmit={handleSubmit(onSubmit)}>
      <BaseWrapper>
        <div className="d-flex align-items-center justify-content-between">
          <Typography variant="body1" gutterBottom component="h1">
            Disable Agent Response
          </Typography>
          <FormIOSSwitch control={control} name={"disable_reply"} />
        </div>
      </BaseWrapper>
      <BaseWrapper>
        <Typography variant="body1" gutterBottom component="h1">
          Restart Chat After
        </Typography>
        <FormTextField
          select
          control={control}
          label="Select Hours"
          name="reset_session_in"
        >
          {[12, 24].map((hours, i) => (
            <MenuItem key={i} value={hours}>
              After {hours} Hours
            </MenuItem>
          ))}
        </FormTextField>
      </BaseWrapper>
      <BaseWrapper>
        <div className="d-flex align-items-center justify-content-between">
          <Typography variant="body1" gutterBottom component="h1">
            Enable Auto Reminder
          </Typography>
          <FormIOSSwitch control={control} name={"auto_reminder.status"} />
        </div>
      </BaseWrapper>
      {formValues.auto_reminder.status && (
        <Fragment>
          <BaseWrapper>
            <div className="d-flex align-items-center justify-content-between">
              <Typography variant="body1" gutterBottom component="h1">
                Send Last Message
              </Typography>
              <FormIOSSwitch
                control={control}
                name={"auto_reminder.send_last_message"}
              />
            </div>
          </BaseWrapper>
          <BaseWrapper>
            <div className={classes.Select_Container}>
              <Typography variant="body1" gutterBottom component="h1">
                Auto Respond to incomplete chat
              </Typography>
              <FormTextField
                select
                label="Select"
                control={control}
                name="auto_reminder.trigger_time"
                variant="filled"
              >
                {[
                  30, 60, 90, 120, 150, 180, 210, 240, 270, 300, 330, 360, 390,
                  420, 450, 480, 510, 540, 570, 600,
                ].map((minutes, i) => (
                  <MenuItem key={i} value={minutes}>
                    {secondsToHms(minutes * 60)}
                  </MenuItem>
                ))}
              </FormTextField>
            </div>
          </BaseWrapper>
          <BaseWrapper>
            <Header
              setValue={setValue}
              control={control}
              header={formValues.auto_reminder.response.header_media}
              defaultFieldName="auto_reminder.response.header_media"
              headerTypes={[
                { value: "image", label: "Image" },
                { value: "video", label: "Video" },
                { value: "file", label: "Document" },
              ]}
            />
          </BaseWrapper>
          <BaseWrapper>
            <Typography variant="body1" gutterBottom component="h1">
              Default Message
            </Typography>
            <FormTextField
              multiline
              rows={5}
              control={control}
              name="auto_reminder.response.default_message"
              placeholder="Default Message"
              onFocus={() => toggleEditingData(true)}
              onBlur={() => toggleEditingData(false)}
            />
          </BaseWrapper>
          <BaseWrapper>
            <div className="d-flex justify-content-between align-items-center mb-2">
              <Typography variant="body1" gutterBottom component="h1">
                Language based Response
              </Typography>
              <Button
                onClick={() => {
                  if (!formValues.auto_reminder.response.languages) {
                    formValues.auto_reminder.response.languages = [];
                  }
                  formValues.auto_reminder.response.languages.push({
                    language: "",
                    message: "",
                    id: Date.now(),
                  });
                  setReloadDrawer(Date.now());
                }}
                className={classes.CustomButton}
              >
                Add Response
              </Button>
            </div>
            {formValues.auto_reminder.response?.languages?.map(
              ({ language, message, id }, i) => {
                return (
                  <StandardCard
                    key={id}
                    closeCard={() => {
                      formValues.auto_reminder.response.languages.splice(i, 1);
                      setValue(
                        "auto_reminder.response.languages",
                        formValues.auto_reminder.response.languages
                      );
                    }}
                    cardTitle={`Language ${i + 1}`}
                  >
                    <BaseWrapper>
                      <FormTextField
                        required
                        control={control}
                        name={`auto_reminder.response.languages[${i}].language`}
                        defaultValue={language}
                        label="Language"
                        onFocus={() => toggleEditingData(true)}
                        onBlur={() => toggleEditingData(false)}
                      />
                    </BaseWrapper>
                    <BaseWrapper>
                      <FormTextField
                        multiline
                        rows={4}
                        control={control}
                        name={`auto_reminder.response.languages[${i}].message`}
                        placeholder="Message"
                        defaultValue={language}
                        onFocus={() => toggleEditingData(true)}
                        onBlur={() => toggleEditingData(false)}
                      />
                    </BaseWrapper>
                  </StandardCard>
                );
              }
            )}
          </BaseWrapper>
        </Fragment>
      )}
      <div className="mt-3 d-flex justify-content-end align-items-end">
        <Button type="submit" className={classes.SaveButton}>
          Save
        </Button>
      </div>
    </form>
  );
};

export default FlowSettings;
