import { MenuItem, Typography } from "@mui/material";
import { FC, Fragment, useEffect, useState } from "react";
import { DropEvent, FileRejection } from "react-dropzone";
import { Control, UseFormSetValue } from "react-hook-form";

import AssetsApi from "Apis/AssetsApi";
import { useFlowData } from "Contexts/ChatFlowDashboard";
import { AssetDirectories } from "Shared/Enums/Asset";
import { HeaderType, MediaProvider } from "Shared/Enums/ChatFlowMsgEnum";
import { FileType } from "Shared/Enums/FileType";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import FormInputField from "SharedComponents/FormComponents/FormInputField";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import StyledDropZone from "../StyledDropZone/StyledDropZone";
import classes from "./FileField.module.css";

interface IFileType {
  value: string;
  label: string;
}

interface FileProps {
  fieldTitle?: string;
  control: Control<any, any>;
  header: ChatFlowMsg.IHeader | undefined;
  headerTypes: IFileType[];
  setValue: UseFormSetValue<any>;
  defaultFieldName?: string;
}

const FileField: FC<FileProps> = ({
  fieldTitle = "",
  control,
  header,
  headerTypes,
  setValue,
  defaultFieldName,
}) => {
  const { toggleEditingData } = useFlowData();
  const [fallbackFile, setFallbackFile] = useState<File[]>([]);

  const onDropHandler = async (
    acceptedFiles: File[],
    fileRejections: FileRejection[],
    event: DropEvent
  ) => {
    if (acceptedFiles.length > 0) {
      const data = new FormData();
      data.append("file", acceptedFiles[0]);
      data.append("max_size", "10000");
      data.append("allowed_ext", FileType.file);
      const res = await AssetsApi.fileUpload(
        AssetDirectories.ENGAGE_ASSETS,
        data
      );

      if (!res.success) {
        return console.log(res.message);
      }
      console.log(res);

      setFallbackFile(
        acceptedFiles.map((file: File) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );

      setValue(
        `${defaultFieldName ? defaultFieldName : "header"}.file_url`,
        res.payload.url
      );

      setValue(`${defaultFieldName ? defaultFieldName : "header"}.file`, {
        file_id: res.payload.id,
        file_name: res.payload.file_name,
        file_size: acceptedFiles[0].size.toString(),
      });

      // set file name
      setValue(
        `${defaultFieldName ? defaultFieldName : "header"}.filename`,
        res.payload.file_name
      );

      setValue(
        `${defaultFieldName ? defaultFieldName : "header"}.uploaded_files`,
        acceptedFiles.map((file: File) =>
          Object.assign(file, {
            fileSize: file.size,
            preview: URL.createObjectURL(file),
          })
        )
      );
    }
  };

  const onRemoveHandler = async () => {
    if (header) {
      if (header && header.file) {
        const { file_id } = header.file;
        const res = await AssetsApi.fileRemove(file_id);
        if (res) {
          setFallbackFile([]);
          setValue(defaultFieldName ? defaultFieldName : "header", {
            type: header.type,
            uploaded_files: [],
            media_provider: MediaProvider.UPLOAD,
          });
        }
      }
    }
  };

  useEffect(() => {
    if (header?.media_provider) {
      setValue(defaultFieldName ? defaultFieldName : "header", {
        type: HeaderType.FILE,
        uploaded_files: [],
        media_provider: header.media_provider,
      });

      setFallbackFile([]);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [header?.media_provider]);

  return (
    <div className={classes.Wrapper}>
      <div className={classes.Select_Container}>
        {fieldTitle !== "" && (
          <div className="d-flex align-items-center justify-content-between">
            <Typography variant="body1" gutterBottom component="h1">
              {fieldTitle}
            </Typography>
          </div>
        )}

        {header && (
          <Fragment>
            <FormTextField
              className="mt-2"
              select
              label="Select Media"
              name={`${
                defaultFieldName ? defaultFieldName : "header"
              }.media_provider`}
              control={control}
            >
              {headerTypes.map(({ value, label }, index) => (
                <MenuItem
                  key={index}
                  value={value}
                  selected={header.type === value}
                >
                  {label}
                </MenuItem>
              ))}
            </FormTextField>

            {header.media_provider &&
              header.media_provider === MediaProvider.UPLOAD && (
                <StyledDropZone
                  fileType={HeaderType.FILE}
                  files={
                    header.uploaded_files?.length > 0
                      ? header.uploaded_files
                      : fallbackFile
                  }
                  removeFile={onRemoveHandler}
                  fileUrl={header.file_url}
                  onChange={onDropHandler}
                />
              )}
            {header.media_provider &&
              header.media_provider === MediaProvider.API && (
                <FormInputField
                  control={control}
                  name="header.api_url"
                  placeholder="API for document"
                  className="mt-3"
                  onFocus={() => toggleEditingData(true)}
                  onBlur={() => toggleEditingData(false)}
                />
              )}
          </Fragment>
        )}
      </div>
    </div>
  );
};

export default FileField;
