import { Input, InputProps } from "@mui/material";
import { FC } from "react";
import { Control, Controller, UseControllerProps } from "react-hook-form";

interface FormInputFieldProps extends UseControllerProps {
  control: Control<any, any>;
}

const FormInputField: FC<InputProps & FormInputFieldProps> = ({
  control,
  defaultValue,
  rules,
  name,
  ...props
}) => {
  return (
    <Controller
      rules={rules}
      name={name}
      control={control}
      render={({ field }) => {
        return (
          <Input
            {...props}
            // to remove console error disabled value with defaultValue
            // value={field.value}
            value={field.value ?? ""}
            onChange={field.onChange}
          />
        );
      }}
    />
  );
};

export default FormInputField;
