import React, { FC } from "react";
import { Control, UseFormSetValue } from "react-hook-form";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import NodeTitle from "../NodeTitle/NodeTitle";
import classes from "./BaseContainer.module.css";

interface BaseContainerProps {
  nodeInfo: string;
  nodeLabel?: string;
  children: React.ReactNode;
  control: Control<ChatFlowMsg.ChatFlowMsgInterface, any>;
  setValue: UseFormSetValue<any>;
}

const BaseContainer: FC<BaseContainerProps> = ({
  children,
  nodeInfo,
  nodeLabel,
  control,
  setValue,
}) => {
  return (
    <div className={classes.Wrapper}>
      <NodeTitle
        nodeInfo={nodeInfo}
        nodeLabel={nodeLabel}
        control={control}
        setValue={setValue}
      />
      {children}
    </div>
  );
};

export default BaseContainer;
