import { useState } from "react";
import {
  Box,
  Button,
  Link,
  ListItem,
  ListItemText,
  Menu,
  MenuItem,
  Typography,
} from "@mui/material";
import { KeyboardArrowDown, KeyboardArrowUp } from "@mui/icons-material";
import classes from "./CreateNewDataField.module.css";
import { useSnacks } from "Contexts/Snack";
import { AuthenticatedReq } from "Utils/Axios";
import { useFlowData } from "Contexts/ChatFlowDashboard";
interface ICreateNewDataField {
  filterValue: string;
  getFieldLabel: (type: string) => string | undefined;
  selectDataField: (fieldName: string) => void;
}

const CreateNewDataField = ({
  filterValue,
  getFieldLabel,
  selectDataField,
}: ICreateNewDataField) => {
  const { setErrorMessage, setSuccessMessage } = useSnacks();
  const { setDataFields } = useFlowData();
  const [creating, setCreating] = useState<boolean>(false);
  const [newDataField, toggleNewDataField] = useState<boolean>(false);
  const [newDataFieldType, setNewDataFieldType] = useState<string>("string");
  const [anchorEl, setAnchorEl] = useState(null);

  const convertFieldName = (filterValue: string) =>
    filterValue.toLowerCase().replace(" ", "_");
  const doCreateLeadDataField = async () => {
    try {
      setCreating(true);
      const res = await AuthenticatedReq(`/lead-data-fields/`, "POST", {
        data: {
          display_name: filterValue,
          field_name: convertFieldName(filterValue),
          field_type: newDataFieldType,
          use_lead_title: false,
          display_in_filter: true,
        },
      });
      if (!res.success) {
        throw Error(res.message);
      }
      setSuccessMessage("New data field created.");
      setDataFields((prevDataFields) => prevDataFields.concat([res.payload]));
      selectDataField(res.payload.field_name);
    } catch (error: any) {
      setErrorMessage(error.message);
    } finally {
      setCreating(false);
    }
  };

  return (
    <ListItem
      className="create-new"
      secondaryAction={
        <Button
          component="button"
          variant="contained"
          aria-label="Create"
          onClick={doCreateLeadDataField}
          disabled={creating}
          sx={{
            bgcolor: "#12B0EE",
            borderRadius: 2,
            boxShadow: "none",
            color: "#ffffff",
            fontSize: 13,
            fontWeight: 500,
            lineHeight: "15px",
            height: 38,
            padding: 1.5,
            textTransform: "capitalize",

            "&:hover": {
              bgcolor: "#289ecb",
            },
          }}
        >
          {"Create"}
        </Button>
      }
    >
      <ListItemText
        primary={
          <Box display="flex" alignItems="baseline">
            <Typography component="span" className={classes.NewFilter}>
              {filterValue}
            </Typography>
            <Button
              component={Link}
              underline="none"
              aria-expanded={newDataField ? "true" : undefined}
              size="small"
              onClick={(event: any) => {
                setAnchorEl(event.currentTarget);
                toggleNewDataField(!newDataField);
              }}
              disableRipple
              endIcon={
                newDataField ? <KeyboardArrowUp /> : <KeyboardArrowDown />
              }
              className={classes.FieldType}
            >
              {getFieldLabel(newDataFieldType)}
            </Button>
            <Menu
              anchorEl={anchorEl}
              open={newDataField}
              onClose={() => {
                setAnchorEl(null);
                toggleNewDataField(false);
              }}
              onClick={() => {
                setAnchorEl(null);
                toggleNewDataField(false);
              }}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "center",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "center",
              }}
            >
              {["string", "numeric", "boolean", "date"].map(
                (fieldType: string, index: number) => (
                  <MenuItem
                    key={index}
                    disableRipple
                    selected={newDataFieldType === fieldType}
                    onClick={() => {
                      setNewDataFieldType(fieldType);
                    }}
                    sx={{ width: 90, justifyContent: "center" }}
                  >
                    {getFieldLabel(fieldType)}
                  </MenuItem>
                )
              )}
            </Menu>
          </Box>
        }
        secondary={`[${convertFieldName(filterValue)}]`}
      />
    </ListItem>
  );
};

export default CreateNewDataField;
