import { MenuItem, Typography } from "@mui/material";
import React, { FC, Fragment, useEffect, useState } from "react";
import { DropEvent, FileRejection } from "react-dropzone";
import { UseFormSetValue } from "react-hook-form";

import AssetsApi from "Apis/AssetsApi";
import { useFlowData } from "Contexts/ChatFlowDashboard";
import { AssetDirectories } from "Shared/Enums/Asset";
import { HeaderType, MediaProvider } from "Shared/Enums/ChatFlowMsgEnum";
import { FileType } from "Shared/Enums/FileType";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import FormInputField from "SharedComponents/FormComponents/FormInputField";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import { IOSSwitch } from "SharedComponents/Switch";
import StyledDropZone from "../StyledDropZone/StyledDropZone";
import classes from "./Header.module.css";

interface IHeaderType {
  value: string;
  label: string;
}
interface HeaderProps {
  askProvider?: boolean;
  control: any;
  header: ChatFlowMsg.IHeader | undefined;
  headerLabel?: string;
  headerTypes: IHeaderType[];
  setValue: UseFormSetValue<any>;
  defaultFieldName?: string;
}

const Header: FC<HeaderProps> = ({
  control,
  header,
  headerLabel = "Header",
  askProvider = false,
  headerTypes,
  setValue,
  defaultFieldName,
}) => {
  const { toggleEditingData } = useFlowData();
  const [fallbackFile, setFallbackFile] = useState<File[]>([]);

  const handleHeader = (
    _event: React.ChangeEvent<HTMLInputElement>,
    checked: boolean
  ) => {
    if (checked)
      setValue(defaultFieldName ? defaultFieldName : "header", {
        type: HeaderType.IMAGE,
        uploaded_files: [],
        media_provider: MediaProvider.UPLOAD,
      });
    else setValue(defaultFieldName ? defaultFieldName : "header", undefined);
  };

  const onDropHandler = async (
    acceptedFiles: File[],
    _fileRejections: FileRejection[],
    _event: DropEvent
  ) => {
    if (acceptedFiles.length > 0 && header) {
      const data = new FormData();
      data.append("file", acceptedFiles[0]);
      data.append("max_size", "2000000");
      if (header.type) {
        const { type } = header;
        switch (type) {
          case "image":
            data.append("allowed_ext", FileType.image);
            break;
          case "video":
            data.append("allowed_ext", FileType.video);
            break;
          case "file":
            data.append("allowed_ext", FileType.file);
            break;
        }
      }
      const res = await AssetsApi.fileUpload(
        AssetDirectories.ENGAGE_ASSETS,
        data
      );

      if (!res.success) {
        return console.log(res.message);
      }
      console.log(res);

      setFallbackFile(
        acceptedFiles.map((file: File) =>
          Object.assign(file, {
            preview: URL.createObjectURL(file),
          })
        )
      );

      setValue(
        `${defaultFieldName ? defaultFieldName : "header"}.file_url`,
        res.payload.url
      );

      setValue(`${defaultFieldName ? defaultFieldName : "header"}.file`, {
        file_id: res.payload.id,
        file_name: res.payload.file_name,
        file_size: acceptedFiles[0].size.toString(),
      });

      setValue(
        `${defaultFieldName ? defaultFieldName : "header"}.uploaded_files`,
        acceptedFiles.map((file: File) =>
          Object.assign(file, {
            fileSize: file.size,
            preview: URL.createObjectURL(file),
          })
        )
      );
    }
  };

  const onRemoveHandler = async () => {
    if (header && header.file) {
      const { file_id } = header.file;
      const res = await AssetsApi.fileRemove(file_id);
      if (res) {
        setFallbackFile([]);
        setValue(defaultFieldName ? defaultFieldName : "header", {
          type: header.type,
          uploaded_files: [],
          media_provider: MediaProvider.UPLOAD,
        });
      }
    }
  };

  useEffect(() => {
    if (header?.type) {
      setValue(defaultFieldName ? defaultFieldName : "header", {
        type: header.type,
        uploaded_files: [],
        media_provider: MediaProvider.UPLOAD,
      });

      setFallbackFile([]);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [header?.type]);

  return (
    <div className={classes.Wrapper}>
      <div className={classes.Select_Container}>
        <div className="d-flex align-items-center justify-content-between">
          <Typography variant="body1" gutterBottom component="h1">
            {headerLabel}
          </Typography>

          <IOSSwitch checked={!!header} onChange={handleHeader} />
        </div>

        {header && (
          <Fragment>
            <FormTextField
              className="mt-2"
              select
              label="Select Media"
              name={`${defaultFieldName ? defaultFieldName : "header"}.type`}
              // value={header.type}
              control={control}
            >
              {headerTypes.map(({ value, label }, index) => (
                <MenuItem
                  key={index}
                  value={value}
                  selected={header.type === value}
                >
                  {label}
                </MenuItem>
              ))}
            </FormTextField>

            {askProvider && (
              <FormTextField
                control={control}
                name="header.media_provider"
                className="mt-3"
                select
                label="Select Provider"
              >
                <MenuItem disabled>None</MenuItem>
                {[
                  {
                    label: "Upload",
                    value: MediaProvider.UPLOAD,
                  },
                  {
                    label: "Dynamic via API",
                    value: MediaProvider.API,
                  },
                ].map(({ label, value }) => (
                  <MenuItem
                    key={value}
                    value={value}
                    selected={header.media_provider === value}
                  >
                    {label}
                  </MenuItem>
                ))}
              </FormTextField>
            )}
            {header.type &&
              header.type !== HeaderType.TEXT &&
              header.media_provider === MediaProvider.UPLOAD && (
                <StyledDropZone
                  fileType={header.type}
                  files={
                    header.uploaded_files?.length > 0
                      ? header.uploaded_files
                      : fallbackFile
                  }
                  removeFile={onRemoveHandler}
                  fileUrl={header.file_url}
                  onChange={onDropHandler}
                />
              )}
            {header.type && header.type === HeaderType.TEXT && (
              <FormTextField
                className="mt-3"
                label="Header text"
                name={`${defaultFieldName ? defaultFieldName : "header"}.text`}
                control={control}
                onFocus={() => toggleEditingData(true)}
                onBlur={() => toggleEditingData(false)}
              />
            )}
            {header.media_provider &&
              header.media_provider === MediaProvider.API && (
                <FormInputField
                  control={control}
                  name="header.api_url"
                  placeholder="API URL"
                  className="mt-3"
                  onFocus={() => toggleEditingData(true)}
                  onBlur={() => toggleEditingData(false)}
                />
              )}
          </Fragment>
        )}
      </div>
    </div>
  );
};

export default Header;
