import { FC } from "react";
import { Handle } from "react-flow-renderer";
import classes from "./NodeHandle.module.css";

interface NodeHandleProps {
  handles: any;
  positionCheck: "bottom" | "top";
  isConnectable: boolean;
  className: "BottomHandle" | "TopHandle";
}

const NodeHandle: FC<NodeHandleProps> = ({
  handles,
  positionCheck,
  isConnectable,
  className,
}) => {
  return handles ? (
    handles.map(({ position, id, style, type }: any) => {
      if (position === positionCheck) {
        return (
          <Handle
            key={id}
            type={type}
            position={position}
            id={id}
            className={`${classes[className]} ${classes.flow__handle}`}
            style={{
              ...style,
            }}
            isConnectable={isConnectable}
          />
        );
      }

      return null;
    })
  ) : (
    <></>
  );
};

export default NodeHandle;
