import { FC, memo, useEffect } from "react";
import { Divider, Typography } from "@mui/material";
import { useForm } from "react-hook-form";

import { useFlowData } from "Contexts/ChatFlowDashboard";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import FormInputField from "SharedComponents/FormComponents/FormInputField";
import { MediaProvider } from "Shared/Enums/ChatFlowMsgEnum";
import BaseContainer from "../../../SharedComponents/BaseContainer/BaseContainer";
import FileField from "../../../SharedComponents/FileField/FileField";
import BaseWrapper from "../../../SharedComponents/BaseWrapper";
import MsgSettings from "../../../SharedComponents/MsgSettings/MsgSettings";
import { FlowMessageProps } from "../types";

import classes from "../Styles.module.css";

const Type104Message: FC<FlowMessageProps> = ({
  nodeInfo,
  nodeLabel,
  reloadDrawer,
  currentMsgIndex,
}) => {
  const { OnChatMsgUpdate, chatMsgs, toggleEditingData } = useFlowData();

  const currentChatMsg = chatMsgs[currentMsgIndex];

  const { control, watch, reset, setValue } = useForm({
    defaultValues: currentChatMsg,
  });

  useEffect(() => {
    if (reloadDrawer) reset(currentChatMsg);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reloadDrawer]);

  const formValues = watch();
  const { header } = formValues;

  useEffect(() => {
    OnChatMsgUpdate(formValues);
  }, [OnChatMsgUpdate, formValues]);

  return (
    <form className={classes.Container}>
      <BaseContainer
        nodeInfo={nodeInfo}
        nodeLabel={nodeLabel}
        control={control}
        setValue={setValue}
      >
        {/* upload document */}
        <BaseWrapper>
          <FileField
            setValue={setValue}
            control={control}
            header={header}
            headerTypes={[
              { value: MediaProvider.UPLOAD, label: "Upload File" },
              { value: MediaProvider.API, label: "Enter API url" },
            ]}
          />
        </BaseWrapper>
        {header && header.media_provider === MediaProvider.UPLOAD && (
          <BaseWrapper>
            <div className="d-flex justify-content-between align-items-center mb-2">
              <Typography variant="body1" gutterBottom component="h1">
                Document Details
              </Typography>
            </div>
            <FormInputField
              className="mt-1"
              placeholder="Name of the document"
              name={`header.filename`}
              control={control}
              onFocus={() => toggleEditingData(true)}
              onBlur={() => toggleEditingData(false)}
            />
          </BaseWrapper>
        )}
        <BaseWrapper>
          <Typography variant="body1" gutterBottom component="h1">
            Error Message
          </Typography>
          <FormTextField
            rows={5}
            multiline
            placeholder="Error Message"
            control={control}
            name="error_msg"
            onFocus={() => toggleEditingData(true)}
            onBlur={() => toggleEditingData(false)}
          />
        </BaseWrapper>
        <Divider className="mt-3" />
        <BaseWrapper>
          <MsgSettings
            setValue={setValue}
            control={control}
            hide={{ save_input: true }}
          />
        </BaseWrapper>
      </BaseContainer>
    </form>
  );
};

export default memo(Type104Message);
