import { useState } from "react";
import { ListItem, List, ListItemText } from "@mui/material";
import clsx from "clsx";

import classes from "./PlaceRecommendationList.module.css";

const PlaceRecommendationList = ({
  predictions,
  google,
  onChange,
  recenterMap,
  isOpen,
  closeList,
}) => {
  // const { updateValue } = useChatFlowDashboard();
  const [selectedLocation, setSelectedLocation] = useState(null);

  const updateLocation = ({ place, main_text, secondary_text }) => {
    const geocoder = new google.maps.Geocoder();

    geocoder.geocode({ placeId: place.place_id }, (results, status) => {
      if (status !== "OK" && results) {
        return;
      }

      const location = results[0].geometry.location;
      onChange({ ...location, address: secondary_text, name: main_text });

      setSelectedLocation(null);
      closeList();
    });
  };

  const getPlaceDetail = ({ place }) => {
    const geocoder = new google.maps.Geocoder();

    geocoder.geocode({ placeId: place.place_id }, (results, status) => {
      if (status !== "OK" && results) {
        return;
      }

      const location = results[0].geometry.location;

      const { lat, lng } = location;

      recenterMap({ lat: lat(), lng: lng() });

      setSelectedLocation(results[0].place_id);
    });
  };

  return (
    <List
      sx={{
        display:
          isOpen && predictions && predictions.length > 0 ? "block" : "none",
      }}
      classes={{ root: classes.List }}
    >
      {predictions.map((place, i) => {
        const { structured_formatting } = place;

        return (
          <ListItem
            button
            classes={{
              root: clsx(
                classes.ListItem,
                selectedLocation === place.place_id && classes.Active
              ),
            }}
            onClick={() => {
              getPlaceDetail({
                place,
              });

              updateLocation({
                place,
                main_text: structured_formatting.main_text,
                secondary_text: structured_formatting.secondary_text,
              });
            }}
            key={place.place_id}
          >
            {/* {place.description} */}
            <ListItemText
              classes={{
                root: clsx(classes.root),
                secondary: clsx(classes.secondary),
              }}
              primary={structured_formatting.main_text}
              secondary={structured_formatting.secondary_text || ""}
            />
          </ListItem>
        );
      })}
    </List>
  );
};

export default PlaceRecommendationList;
