import { FC, useMemo } from "react";
import { DropEvent, FileRejection, useDropzone } from "react-dropzone";
import classes from "./StyledDropZone.module.css";
import { acceptStyle, activeStyle, baseStyle, rejectStyle } from "./styles";
import { VideoZone, ImageZone } from "./Zones";
import DocumentZone from "./Zones/DocumentZone";

export const acceptedFilesTypes = {
  image: {
    label: "PNG / JPG",
    value: { "image/*": [] },
    component: ({ file, removeFile, fileUrl }: any) => (
      <ImageZone file={file} removeFile={removeFile} fileUrl={fileUrl} />
    ),
  },
  video: {
    label: "MP4 / 3GP / 4K",
    value: { "video/*": [] },
    component: ({ file, removeFile, fileUrl }: any) => (
      <VideoZone file={file} removeFile={removeFile} fileUrl={fileUrl} />
    ),
  },
  file: {
    label: "DOC / XLS / PDF / ZIP",
    value: {
      // "*": [".doc,.docx,.pdf,.xls,.xlsx,.ppt,.pptx,.zip"],
      "application/msword": [".doc"],
      "application/vnd.openxmlformats-officedocument.wordprocessingml.document":
        [".docx"],
      "application/vnd.ms-powerpoint": [".ppt"],
      "application/vnd.openxmlformats-officedocument.presentationml.presentation":
        [".pptx"],
      "application/vnd.ms-excel": [],
      "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet": [
        ".xlsx",
      ],
      "text/plain": [],
      "application/pdf": [],
      "application/x-zip-compressed": [],
    },
    component: ({ file, removeFile }: any) => (
      <DocumentZone file={file} removeFile={removeFile} />
    ),
  },
};

interface StyledDropZoneProps {
  files: any[];
  onChange: (
    acceptedFiles: File[],
    fileRejections: FileRejection[],
    event: DropEvent
  ) => Promise<void>;
  fileType: "image" | "video" | "file";
  removeFile: () => void;
  fileUrl?: string;
}

const StyledDropZone: FC<StyledDropZoneProps> = ({
  files,
  onChange,
  fileType,
  removeFile,
  fileUrl,
}) => {
  const {
    getRootProps,
    getInputProps,
    isDragActive,
    isDragAccept,
    isDragReject,
    open,
  } = useDropzone({
    maxFiles: 1,
    maxSize: 9e6,
    accept: acceptedFilesTypes[fileType].value as any,
    noClick: true,
    noKeyboard: true,
    onDrop: onChange,
  });

  const style: React.CSSProperties = useMemo(
    () => ({
      ...baseStyle,
      height: fileType === "file" ? "120px" : "244px",
      ...(isDragActive ? activeStyle : {}),
      ...(isDragAccept ? acceptStyle : {}),
      ...(isDragReject ? rejectStyle : {}),
    }),
    [fileType, isDragActive, isDragAccept, isDragReject]
  );

  return (
    <div className="mt-2">
      {files.length === 0 ? (
        <div className={classes.PreFile} onClick={open}>
          <div {...getRootProps({ style })}>
            <input {...getInputProps()} />
            <div className={classes["input-text"]}>
              <h1>Upload Header {fileType}</h1>
              <p>{acceptedFilesTypes[fileType]?.label}</p>
            </div>
          </div>
        </div>
      ) : (
        <div className={classes.File_Added}>
          {files.map((file: { preview: string }, i) => {
            return (
              <span key={i}>
                {acceptedFilesTypes[fileType].component({
                  file,
                  removeFile,
                  fileUrl,
                })}
              </span>
            );
          })}
        </div>
      )}
    </div>
  );
};

export default StyledDropZone;
