import { Fragment } from "react";
import { Helmet } from "react-helmet-async";

import FlowRenderer from "Containers/ChatFlowDashboard/FlowRenderer/FlowRenderer";
import FlowControls from "Components/ChatFlowDashboard/FlowControls/FlowControls";
import FlowDrawer from "Containers/ChatFlowDashboard/FlowDrawer/FlowDrawer";

const ChatFlowDashboard = () => {
  return (
    <Fragment>
      <Helmet>
        <title>Chat Flow Dashboard</title>
      </Helmet>
      <div className="d-flex">
        <FlowRenderer />
        <FlowControls />
        <FlowDrawer />
      </div>
    </Fragment>
  );
};

export default ChatFlowDashboard;
