import { FC } from "react";
import { useNavigate } from "react-router-dom";
import classes from "./NavigationBar.module.css";
import { Save, Logout } from "@mui/icons-material";
import { Button, Grid, Typography } from "@mui/material";
import { useFlowData } from "Contexts/ChatFlowDashboard";

interface NavigationBarProps {}

const NavigationBar: FC<NavigationBarProps> = () => {
  const navigate = useNavigate();
  const { saveChatFlowData } = useFlowData();
  return (
    <Grid
      maxWidth="xl"
      sx={{ p: 1.2, pl: 4, pr: 4, maxWidth: "100%" }}
      className={classes.Wrapper}
    >
      <img
        src="https://storage.googleapis.com/easy-social-assets/ChatFlow/logo.svg"
        alt="logo"
      />

      <div className={classes.Buttons}>
        <Button
          startIcon={<Save color="disabled" />}
          className={classes.SaveButton}
          onClick={saveChatFlowData}
        >
          <Typography variant="body1" color="GrayText">
            Save
          </Typography>
        </Button>

        <Button
          startIcon={<Logout color="error" />}
          variant="text"
          onClick={() => {
            window.history.length > 1
              ? navigate(-1)
              : navigate("/business/general-settings", { replace: true });
          }}
        >
          <Typography variant="body1" color="red">
            Exit
          </Typography>
        </Button>
      </div>
    </Grid>
  );
};

export default NavigationBar;
