import {
  Box,
  Collapse,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Radio,
  Typography,
} from "@mui/material";

import { IListDataField } from "Shared/Interfaces/LeadDataFieldsInterface";
import { useFlowData } from "Contexts/ChatFlowDashboard";
import CreateNewDataField from "./CreateNewDataField";
import { CollapseStyle, TitleStyle, TypeStyle } from "./ListDataFieldStyles";

const ListDataField = ({
  openList,
  selectDataField,
  selectedField,
  filterValue = "",
}: IListDataField) => {
  const { dataFields } = useFlowData();
  const filteredDataFields = dataFields.filter((field) =>
    field.field_name.toLowerCase().includes(filterValue.toLowerCase())
  );
  const exactMatchFields = filteredDataFields.filter(
    (field: any) =>
      field.field_name.toLowerCase() === filterValue?.toLowerCase()
  );

  const getFieldLabel = (type: string) =>
    ({
      numeric: "Number",
      string: "Text",
      boolean: "Boolean",
      date: "Date",
    }[type]);

  return (
    <Collapse in={openList} sx={CollapseStyle}>
      <List className={openList ? "opened-list" : ""}>
        {filteredDataFields.map((item: any, i: number) => (
          <ListItem key={i}>
            <ListItemButton
              onClick={() => {
                selectDataField(item.field_name);
              }}
              selected={selectedField === item.field_name}
              defaultValue={item.field_name}
            >
              <ListItemIcon>
                <Radio
                  edge="start"
                  checked={selectedField === item.field_name}
                  disableRipple
                />
              </ListItemIcon>
              <ListItemText
                primary={
                  <Box display="flex" alignItems="baseline">
                    <Typography component="span" sx={TitleStyle}>
                      {item.display_name}
                    </Typography>
                    <Typography
                      variant="caption"
                      component="small"
                      sx={TypeStyle}
                    >
                      {getFieldLabel(item.field_type)}
                    </Typography>
                  </Box>
                }
                secondary={`[${item.display_name
                  .toLowerCase()
                  .replace(" ", "_")}]`}
              />
            </ListItemButton>
          </ListItem>
        ))}
        {filterValue.length >= 3 &&
          filteredDataFields &&
          exactMatchFields.length === 0 && (
            <CreateNewDataField
              {...{ filterValue, getFieldLabel, selectDataField }}
            />
          )}
      </List>
    </Collapse>
  );
};

export default ListDataField;
