declare global {
  interface Document {
    selection?: any;
  }
}

export const getCaretPosition = (
  ctrl: any
): {
  start: number;
  end: number;
} => {
  // IE < 9 Support
  const doc: Document = document;
  if (doc.selection) {
    ctrl.focus();
    const range: any = doc.selection.createRange();
    const rangeLen: number = range.text.length;
    range.moveStart("character", -ctrl.value.length);
    const start: number = range.text.length - rangeLen;
    return {
      start: start,
      end: start + rangeLen,
    };
  }
  // IE >=9 and other browsers
  else if (ctrl.selectionStart || ctrl.selectionStart === "0") {
    ctrl.focus();
    return {
      start: ctrl.selectionStart,
      end: ctrl.selectionEnd,
    };
  } else {
    return {
      start: 0,
      end: 0,
    };
  }
};

export const setCaretPosition = (
  ctrl: any,
  start: number,
  end: number
): void => {
  // IE >= 9 and other browsers
  if (ctrl.setSelectionRange) {
    ctrl.focus();
    ctrl.setSelectionRange(start, end);
  }
  // IE < 9
  else if (ctrl.createTextRange) {
    const range: any = ctrl.createTextRange();
    range.collapse(true);
    range.moveEnd("character", end);
    range.moveStart("character", start);
    range.select();
  }
};
