import NodeService from "Services/ChatFlowService/NodeService";
import {
  Type100Message,
  Type101Message,
  Type102Message,
  Type103Message,
  Type104Message,
  Type105Message,
  Type107Message,
  Type108Message,
  Type109Message,
  Type110Message,
  Type111Message,
  Type112Message,
  Type113Message,
} from "./AvailableFlowMsgs";

export const nodeLabel = (code: number): string => {
  const result = NodeService.all_nodes_list.find((o) => o.code === code);
  if (!result) return "";
  return result.label;
};

export const NodeInfo = (code: number): string => {
  const result = NodeService.all_nodes_list.find((o) => o.code === code);
  if (!result) return "";
  return result.description;
};

const FlowMessageHandler = ({
  code,
  currentMsgIndex,
  reloadDrawer,
}: {
  code: number;
  currentMsgIndex: number;
  reloadDrawer: number;
}) => {
  switch (code) {
    case 100:
      return (
        <Type100Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 101:
      return (
        <Type101Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 102:
      return (
        <Type102Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 103:
      return (
        <Type103Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 104:
      return (
        <Type104Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 105:
      return (
        <Type105Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 107:
      return (
        <Type107Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 108:
      return (
        <Type108Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 109:
      return (
        <Type109Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 110:
      return (
        <Type110Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 111:
      return (
        <Type111Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 112:
      return (
        <Type112Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    case 113:
      return (
        <Type113Message
          reloadDrawer={reloadDrawer}
          nodeInfo={NodeInfo(code)}
          nodeLabel={nodeLabel(code)}
          currentMsgIndex={currentMsgIndex}
        />
      );
    default:
      return <p>Invalid Type</p>;
  }
};

export default FlowMessageHandler;
