import { FC, memo, useEffect } from "react";
import { Typography } from "@mui/material";
import { useForm } from "react-hook-form";

import { useFlowData } from "Contexts/ChatFlowDashboard";
import StandardCard from "Components/ChatFlowDashboard/FlowDrawer/SharedComponents/StandardCard/StandardCard";
import { ValidationType } from "Shared/Enums/ChatFlowMsgEnum";
import FormInputField from "SharedComponents/FormComponents/FormInputField";
import BaseContainer from "../../../SharedComponents/BaseContainer/BaseContainer";
import BaseWrapper from "../../../SharedComponents/BaseWrapper";
import { FlowMessageProps } from "../types";

import classes from "../Styles.module.css";

const Type112Message: FC<FlowMessageProps> = ({
  nodeInfo,
  nodeLabel,
  reloadDrawer,
  currentMsgIndex,
}) => {
  const { OnChatMsgUpdate, chatMsgs, toggleEditingData } = useFlowData();
  const currentChatMsg = chatMsgs[currentMsgIndex];

  const { control, watch, reset, setValue } = useForm({
    defaultValues: currentChatMsg,
  });

  useEffect(() => {
    if (reloadDrawer) reset(currentChatMsg);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [reloadDrawer]);

  const formValues = watch();
  const { condition } = formValues;

  useEffect(() => {
    OnChatMsgUpdate(formValues);
  }, [OnChatMsgUpdate, formValues]);

  return (
    <form className={classes.Container}>
      {condition && (
        <BaseContainer
          nodeInfo={nodeInfo}
          nodeLabel={nodeLabel}
          control={control}
          setValue={setValue}
        >
          <BaseWrapper>
            <Typography
              variant="body1"
              gutterBottom
              component="h1"
              className="mb-2"
            >
              API URL
            </Typography>
            <FormInputField
              placeholder="eg: https://yourapi.com"
              control={control}
              name="api_url"
              className="mb-3"
              onFocus={() => toggleEditingData(true)}
              onBlur={() => toggleEditingData(false)}
            />
          </BaseWrapper>
          <BaseWrapper>
            {condition.conditions?.map(({ style, validation_type }, i) => (
              <StandardCard
                key={i}
                enableClose={false}
                style={{
                  ...style,
                  textAlign: "center",
                  marginLeft: "24px",
                  marginRight: "24px",
                }}
              >
                <Typography variant="body1" component="p">
                  {validation_type === ValidationType.SUCCESS
                    ? `True Branch`
                    : `False Branch`}
                </Typography>
              </StandardCard>
            ))}
          </BaseWrapper>
        </BaseContainer>
      )}
    </form>
  );
};

export default memo(Type112Message);
