import React, { FC, memo, useState } from "react";
import classes from "./CustomDefaultNode.module.css";
import { NodeProps } from "react-flow-renderer";
import LabelItem from "./LabelItem/LabelItem";
import clsx from "clsx";
import NodeHandle from "./NodeHandle/NodeHandle";
import { Typography } from "@mui/material";
import { ChatFlowNode } from "Shared/Interfaces/ChatFlowNodeInterface";
import NodeService from "Services/ChatFlowService/NodeService";

interface CustomDefaultNodeProps extends NodeProps {
  data: {
    label: any;
    handles?: ChatFlowNode.IHandleInterface[];
    type: number;
    isConnectable?: boolean;
    style?: React.CSSProperties;
    nodeDataLabel?: string;
  };
}

const CustomDefaultNode: FC<CustomDefaultNodeProps> = ({ data, selected }) => {
  const { handles, style, label } = data;

  const [node] = useState(
    NodeService.all_nodes_list.find((e) => e.code === data.type)
  );

  return (
    <div
      className={clsx(classes.CustomNode, selected && classes.Active)}
      style={{ ...style }}
    >
      {/* Top handles */}
      <div className={classes.TopHandle_Wrapper}>
        <NodeHandle
          className="TopHandle"
          handles={handles}
          isConnectable={true}
          positionCheck="top"
        />
        {data.nodeDataLabel === "FIRST" && (
          <div className={clsx(classes.StartType, classes.NodeDataLabel)}>
            <Typography variant="overline" component="p">
              START
            </Typography>
          </div>
        )}
      </div>

      <div className="d-flex align-items-center gap-2 justify-content-center">
        {/*TODO: refactor and fix this component */}
        {node?.Icon()}
        &nbsp; &nbsp;
        <LabelItem {...(label as any)} />
      </div>

      {/* Bottom handles */}
      <div className={classes.BottomHandle_Wrapper}>
        <NodeHandle
          handles={handles}
          positionCheck="bottom"
          className="BottomHandle"
          isConnectable={true}
        />

        {data.nodeDataLabel === "LAST" && (
          <div className={clsx(classes.EndType, classes.NodeDataLabel)}>
            <Typography variant="overline" component="p">
              END
            </Typography>
          </div>
        )}
      </div>
    </div>
  );
};

export default memo(CustomDefaultNode);
