import { MiniMap } from "react-flow-renderer";

const FlowMiniMap = () => {
  return (
    <MiniMap
      nodeStrokeColor={(n) => {
        if (n.style?.background) return n.style.background.toString();
        if (n.type === "customDefault") return "#0041d0";
        return "#eee";
      }}
      nodeColor={(n) => {
        if (n.style?.background) return n.style.background.toString();
        return "#fff";
      }}
      nodeBorderRadius={2}
      style={{ right: "unset", left: 45 }}
    />
  );
};

export default FlowMiniMap;
