import { FC, useEffect, useState } from "react";
import {
  Box,
  ClickAwayListener,
  IconButton,
  InputAdornment,
} from "@mui/material";
import { Close } from "@mui/icons-material";
import { Control, UseFormSetValue } from "react-hook-form";

import { useFlowData } from "Contexts/ChatFlowDashboard";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import FormTextField from "SharedComponents/FormComponents/FormTextField";

import ListDataField from "./ListDataField";

interface SelectDataFieldProps {
  control: Control<ChatFlowMsg.ChatFlowMsgInterface, any>;
  data_field: string;
  setValue: UseFormSetValue<ChatFlowMsg.ChatFlowMsgInterface>;
}

const SelectDataField: FC<SelectDataFieldProps> = ({
  control,
  data_field,
  setValue,
}) => {
  const { toggleEditingData } = useFlowData();
  const [selectedField, setSelectedField] = useState<string>("");
  const [openList, toggleList] = useState(false);

  const selectDataField = (fieldName: string) => {
    setSelectedField(fieldName);
    setValue("msg_settings.data_field", fieldName);
    setTimeout(() => toggleList(false), 200);
  };

  useEffect(() => {
    setSelectedField(data_field);
  }, [data_field]);

  return (
    <ClickAwayListener onClickAway={() => toggleList(false)}>
      <Box position="relative">
        <FormTextField
          control={control}
          name="msg_settings.data_field"
          label="Select Lead Data Field"
          variant="filled"
          onFocus={() => {
            toggleEditingData(true);
            toggleList(true);
          }}
          onBlur={() => toggleEditingData(false)}
          // onBlur={() => toggleList(false)}
          inputProps={{
            maxLength: 30,
          }}
          InputProps={{
            autoComplete: "off",
            endAdornment: (
              <InputAdornment position="end">
                <IconButton
                  aria-label="Clear"
                  edge="end"
                  onClick={() => {
                    setValue("msg_settings.data_field", "");
                    setSelectedField("");
                    toggleList(!openList);
                  }}
                >
                  <Close />
                </IconButton>
              </InputAdornment>
            ),
          }}
        />
        <ListDataField
          openList={openList}
          {...{
            selectDataField,
            selectedField,
            filterValue: data_field,
          }}
        />
      </Box>
    </ClickAwayListener>
  );
};

export default SelectDataField;
