import { FC } from "react";
import classes from "./NoteText.module.css";
import { Typography } from "@mui/material";
import clsx from "clsx";

interface NoteTextProps {
  body: string;
  caption?: string;
  className?: any;
}

const NoteText: FC<NoteTextProps> = ({ body, caption, className }) => {
  return (
    <div className={clsx(classes.Wrapper, className)}>
      <Typography className={classes.bodyText} variant="body2" gutterBottom>
        {body}
      </Typography>
      {caption && (
        <Typography
          variant="body2"
          className={classes.SubBodyText}
          gutterBottom
          color="GrayText"
        >
          {caption}
        </Typography>
      )}
    </div>
  );
};

export default NoteText;
