import { memo, useCallback, useEffect } from "react";
import { MenuItem, Typography } from "@mui/material";
import { Control, UseFormSetValue } from "react-hook-form";

import { useFlowData } from "Contexts/ChatFlowDashboard";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import FormCheckBox from "SharedComponents/FormComponents/FormCheckBox";
import FormTextField from "SharedComponents/FormComponents/FormTextField";
import NoteText from "../NoteText/NoteText";
import SelectDataField from "./SelectDataField/SelectDataField";

interface CaptureInputProps {
  disableCapture?: boolean;
  control: Control<ChatFlowMsg.ChatFlowMsgInterface, any>;
  setValue: UseFormSetValue<ChatFlowMsg.ChatFlowMsgInterface>;
  [x: string]: any;
}

const response_types = [
  { label: "Text", value: "text" },
  { label: "Location", value: "location" },
];

const CaptureInput = memo<CaptureInputProps>(
  ({
    control,
    disableCapture,
    save_input,
    response_type,
    data_field,
    setValue,
  }) => {
    const { toggleEditingData } = useFlowData();
    // const { _getWatch } = control;
    // const response_type = _getWatch("msg_settings.response_type");

    const reCaptureInput = useCallback(() => {
      if (response_type !== "text") {
        setValue("msg_settings.save_input", false);
        setValue("msg_settings.data_field", "");
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [response_type]);

    useEffect(() => {
      reCaptureInput();
    }, [reCaptureInput]);

    return (
      <div>
        <Typography
          variant="body1"
          gutterBottom
          component="h1"
          className="mb-2"
        >
          Capture User Input
        </Typography>

        <FormTextField
          select
          control={control}
          disabled={disableCapture}
          label={
            typeof response_type !== "undefined" && response_type !== ""
              ? "Accept " + response_type
              : "Select User Input"
          }
          name="msg_settings.response_type"
          onFocus={() => toggleEditingData(true)}
          onBlur={() => toggleEditingData(false)}
        >
          <MenuItem value="">
            <em>None</em>
          </MenuItem>
          {response_types.map((item, i) => (
            <MenuItem key={i} value={item.value}>
              Accept {item.label}
            </MenuItem>
          ))}
        </FormTextField>

        {response_type === "text" && (
          <div>
            <FormCheckBox
              className="mt-3"
              control={control}
              name="msg_settings.save_input"
              label="Save user input value"
            />

            {save_input && (
              <NoteText
                className="mb-3 p-2"
                body="You can save user input in one of the parameters listed below. The saved data can be used for analytics or displaying this information dynamically in a conversation."
              />
            )}
          </div>
        )}

        {response_type === "text" && save_input && (
          <SelectDataField
            control={control}
            setValue={setValue}
            data_field={data_field}
          />
        )}
      </div>
    );
  }
);

export default CaptureInput;
