import { ExpandLess, ExpandMore } from "@mui/icons-material";
import { Collapse, List, ListItem, Typography } from "@mui/material";
import clsx from "clsx";
import { Fragment, useEffect, useState } from "react";
import NodeService from "Services/ChatFlowService/NodeService";
import classes from "./NodeList.module.css";
import NodeListItem from "./NodeListItem/NodeListItem";

const NodeList = () => {
  const [customList, setCustomList] = useState<{ [key: string]: any }>({});
  const [expanded, setExpanded] = useState<string | false>("Basic");

  const handleChange = (_event: React.SyntheticEvent, panel: string) => {
    setExpanded(expanded !== panel ? panel : false);
  };

  const onDragStart = (event: any, nodeType: any) => {
    event.dataTransfer.setData("application/reactflow", nodeType);
    event.dataTransfer.effectAllowed = "move";
  };

  useEffect(() => {
    let customListArr: { [key: string]: any } = {};

    const node_list = NodeService.all_nodes_list;

    node_list.forEach((nodeType) => {
      if (!customListArr[nodeType.type]) {
        const nodes = node_list.filter((e) => e.type === nodeType.type);
        customListArr[nodeType.type] = nodes;
      }
    });

    setCustomList(customListArr);
  }, []);

  return (
    <List>
      {Object.keys(customList).map((item, i) => {
        const open = expanded === item;
        return (
          <Fragment key={i}>
            <ListItem
              onClick={(e) => handleChange(e, item)}
              button
              className={clsx(
                classes.RootListItem,
                "d-flex justify-content-between align-items-center mb-3"
              )}
            >
              <Typography
                aria-controls="panel1d-content"
                id="panel1d-header"
                variant="h6"
                component="h1"
              >
                {item} Messages
              </Typography>
              {open ? <ExpandLess /> : <ExpandMore />}
            </ListItem>

            <Collapse
              key={i + 100}
              in={open}
              timeout="auto"
              unmountOnExit
              sx={{ mb: 2 }}
            >
              <List
                component="div"
                disablePadding
                className={classes.SubList}
                sx={{ display: "flex", flexDirection: "column", rowGap: 2 }}
              >
                {customList[item].map((subItem: any, j: number) => (
                  <NodeListItem
                    {...subItem}
                    onDragStart={onDragStart}
                    key={j}
                  />
                ))}
              </List>
            </Collapse>
          </Fragment>
        );
      })}
    </List>
  );
};

export default NodeList;
