import CustomIcon from "SharedComponents/CustomIcon/CustomIcon";

const style = { color: "#8E8E8E" };

export default class NodeService {
  // constructor(private type: number) {}

  public static get all_nodes_list() {
    return [
      {
        label: "Basic Message",
        code: 102,
        Icon: () => CustomIcon.ChatBasic({ style }),
        type: "Basic",
        description:
          "This is the most basic node, it allows sending a message that contains text, image and video. It can accept multiple data responses.",
      },
      {
        label: "Geo Location",
        code: 103,
        Icon: () => CustomIcon.Location({ style }),
        type: "Basic",
        description:
          "This node allows you to manually feed in geo location of a place. When this message is delivered, it provides a preview and link to google maps of that location. ",
      },
      {
        label: "Document",
        code: 104,
        Icon: () => CustomIcon.Document({ style }),
        type: "Basic",
        description:
          "This node allows you to upload a file in multiple formats like PDF, Word, Excel, PPT, Etc. When this message is delivered, it allows user to download the file. ",
      },
      {
        label: "Interactive Text Response",
        code: 100,
        Icon: () => CustomIcon.ChatAB({ style }),
        type: "Interactive",
        description:
          "This message allows same properties of a Basic Message node and additionally accepts custom short response and saves its corresponding value. eg. A = Jazz, B = Hip Hop ",
      },
      {
        label: "Interactive Text Response - Branched",
        code: 101,
        Icon: () => CustomIcon.ChatInteractive({ style }),
        type: "Interactive",
        description:
          "This message works exactly like Interactive Text Response node and additionally allows to navigate to a different chat branch based on the short response entered by the user.",
      },
      {
        label: "Interactive Button Response",
        code: 110,
        Icon: () => CustomIcon.ClickedGesture({ style }),
        type: "Interactive",
        description:
          "This type of node displays options in the form of buttons, which ever button is clicked is sent back as response. It allows navigation to a different chat branch based on the response entered.",
      },
      {
        label: "Interactive List Response",
        code: 111,
        Icon: () => CustomIcon.List({ style }),
        type: "Interactive",
        description:
          "When there is a need to show more than 3 responses (upto 10) then node is preferred. It pops up a list of options from which one is selected and it navigates the user on chat branch based on his choice.",
      },
      {
        label: "If and Else Condition",
        code: 109,
        Icon: () => CustomIcon.Signpost({ style }),
        type: "Conditional",
        description:
          "This node checks if the value entered by a user matches a certain condition. On meeting a condition it directs the conversation to a specific chat branch.",
      },
      {
        label: "True or False Condition",
        code: 108,
        Icon: () => CustomIcon.CircleAdvanced({ style }),
        type: "Conditional",
        description:
          "This node checks if a value is already available in the current or previous  conversation. Eg. you can check if name field is empty or not to conclude if a user is a returning user or a new user.",
      },
      {
        label: "API Basic Message",
        code: 105,
        Icon: () => CustomIcon.ChatBasic({ style: { color: "#6070C0" } }),
        type: "Dynamic API",
        description:
          "This node allows sending a dynamic message that contains text, image and video via an API. It can accept multiple data responses.",
      },
      {
        label: "API Interactive Messages",
        code: 107,
        Icon: () => CustomIcon.ChatAB({ style: { color: "#6070C0" } }),
        type: "Dynamic API",
        description:
          "This node is often used when there is massive huge data of items and for each there is a different set of option available. This node allows to create dynamic short response in real time for each item to give extreme flexibility. ",
      },
      {
        label: "API True or False",
        code: 112,
        Icon: () => CustomIcon.CircleAdvanced({ style: { color: "#6070C0" } }),
        type: "Dynamic API",
        description:
          "This node allows user to pass values entered by a user during the conversation to an API endpoint and return true or false as a response based on an evaluation carried out externally via API.",
      },
      {
        label: "API Interactive List Response",
        code: 113,
        Icon: () => CustomIcon.List({ style: { color: "#6070C0" } }),
        type: "Dynamic API",
        description:
          "When there is a need to show more than 3 responses (upto 10) then node is preferred. It pops up a list of options from which one is selected and it navigates the user on chat branch based on his choice.",
      },
    ];
  }
}
