import {
  ChangeEvent,
  FC,
  Fragment,
  memo,
  MouseEvent,
  useEffect,
  useRef,
  useState,
} from "react";
import {
  Box,
  Button,
  Divider,
  InputAdornment,
  Menu,
  MenuItem,
  SvgIcon,
  TextField,
  Typography,
} from "@mui/material";
import { Control, UseFormSetValue } from "react-hook-form";

import { getCaretPosition, setCaretPosition } from "Utils/Caret";
import { useFlowData } from "Contexts/ChatFlowDashboard";
import { ChatFlowMsg } from "Shared/Interfaces/ChatFlowMsgInterface";
import CustomIcon from "SharedComponents/CustomIcon/CustomIcon";
import FormTextField from "SharedComponents/FormComponents/FormTextField";

import classes from "./MessageFields.module.css";

interface MessageFieldsProps {
  fieldLabel?: string;
  leadVariableLabel?: string;
  control: Control<ChatFlowMsg.ChatFlowMsgInterface, any>;
  setValue: UseFormSetValue<any>;
  disable_primary?: boolean;
  disable_pre?: boolean;
  disable_post?: boolean;
  disable_error?: boolean;
}

interface Positions {
  field: string;
  start: number;
  end: number;
}

const MessageFields: FC<MessageFieldsProps> = memo(
  ({
    fieldLabel = "Message",
    leadVariableLabel = "Lead Data Fields",
    control,
    setValue,
    disable_primary = false,
    disable_pre = true,
    disable_post = true,
    disable_error = false,
  }) => {
    const { dataFields, toggleEditingData } = useFlowData();
    const [caretPos, setCaretPos] = useState<number>(0);
    const [fieldsFilter, setFieldsFilter] = useState<string>("");
    const [currentElement, setCurrentElement] = useState<string>("");
    const [filteredFields, setFilteredFields] = useState<any[]>([]);
    const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
    const textFieldRef = useRef<HTMLTextAreaElement>();
    const handleClickListItem = (
      event: MouseEvent<HTMLButtonElement>,
      elementName: string
    ) => {
      setCurrentElement(elementName);
      setAnchorEl(event.currentTarget);
    };
    const handleCloseListItem = () => {
      setCurrentElement("");
      setAnchorEl(null);
    };
    const valuePositionCheck = (value: string) => {
      const fieldMatcher: RegExp = /\[(.*?)\]/gm;
      const matches: RegExpMatchArray | null = value.match(fieldMatcher);
      if (matches) {
        let match;
        const positions: Positions[] = [];
        while ((match = fieldMatcher.exec(value))) {
          positions.push({
            field: match[0],
            start: fieldMatcher.lastIndex - match[0].length,
            end: fieldMatcher.lastIndex,
          });
        }
        const [conflictedPos]: Positions[] = positions.filter(
          (pos) => caretPos >= pos.start && caretPos <= pos.end
        );
        if (conflictedPos) {
          return conflictedPos.end;
        } else {
          return caretPos;
        }
      } else {
        return caretPos;
      }
    };
    const handleDataFieldSelect = (dataFieldValue: string) => {
      handleCloseListItem();
      const currentElementValue: string = control._getWatch(currentElement);
      const insertPos = valuePositionCheck(currentElementValue);
      const dataField = `[${dataFieldValue}]`;
      const newElementValue = [
        currentElementValue.slice(0, insertPos),
        dataField,
        currentElementValue.slice(insertPos),
      ].join("");
      const newCaretPos = insertPos + dataField.length;
      setValue(currentElement, newElementValue);
      setCaretPosition(textFieldRef.current, newCaretPos, newCaretPos);
      setCaretPos(newCaretPos);
    };

    useEffect(() => {
      setFilteredFields((filteredFields) =>
        fieldsFilter !== ""
          ? filteredFields.filter(({ value }) => value.includes(fieldsFilter))
          : [...(dataFields ? dataFields : [])]
      );
    }, [dataFields, fieldsFilter]);

    useEffect(() => {
      ["keyup", "click"].forEach((e: string) => {
        textFieldRef.current?.addEventListener(e, () =>
          setCaretPos(getCaretPosition(textFieldRef.current).end)
        );
      });
    }, []);

    return (
      <Fragment>
        <div className={classes.Message_Wrapper}>
          {(!disable_primary || !disable_pre || !disable_post) && (
            <div className="d-flex justify-content-between align-items-center mb-3">
              <Typography variant="body1" gutterBottom component="h1">
                {fieldLabel}
              </Typography>
            </div>
          )}
          {!disable_pre && (
            <Box className={classes.Message_Field}>
              <FormTextField
                rows={5}
                multiline
                placeholder="Intro Message"
                control={control}
                name="pre_msg"
                inputProps={{ ref: textFieldRef }}
                InputProps={{ sx: { pb: 30 + "px !important" } }}
                onFocus={() => toggleEditingData(true)}
                onBlur={() => toggleEditingData(false)}
              />
              <Button
                className={classes.CustomButton}
                size="small"
                onClick={(event) => handleClickListItem(event, "pre_msg")}
              >
                {leadVariableLabel}
                <SvgIcon
                  viewBox="0 0 10 6"
                  sx={{ color: "text.secondary", width: 10, height: 6, ml: 1 }}
                >
                  <path
                    fillRule="evenodd"
                    clipRule="evenodd"
                    d="M5 6L10 0H0L5 6Z"
                  />
                </SvgIcon>
              </Button>
            </Box>
          )}
          {!disable_post && (
            <Box className={classes.Message_Field}>
              <FormTextField
                rows={5}
                multiline
                placeholder="Outro Message"
                control={control}
                name="post_msg"
                inputProps={{ ref: textFieldRef }}
                InputProps={{ sx: { pb: 30 + "px !important" } }}
                onFocus={() => toggleEditingData(true)}
                onBlur={() => toggleEditingData(false)}
              />
              <Button
                className={classes.CustomButton}
                size="small"
                onClick={(event) => handleClickListItem(event, "post_msg")}
              >
                {leadVariableLabel}
                <SvgIcon
                  viewBox="0 0 10 6"
                  sx={{ color: "text.secondary", width: 10, height: 6, ml: 1 }}
                >
                  <path
                    fillRule="evenodd"
                    clipRule="evenodd"
                    d="M5 6L10 0H0L5 6Z"
                  />
                </SvgIcon>
              </Button>
            </Box>
          )}
          {!disable_primary && (
            <Box className={classes.Message_Field}>
              <FormTextField
                rows={5}
                multiline
                placeholder="Primary Message"
                control={control}
                name="primary_msg"
                inputProps={{ ref: textFieldRef }}
                InputProps={{ sx: { pb: 30 + "px !important" } }}
                onFocus={() => toggleEditingData(true)}
                onBlur={() => toggleEditingData(false)}
              />
              <Button
                className={classes.CustomButton}
                size="small"
                onClick={(event) => handleClickListItem(event, "primary_msg")}
              >
                {leadVariableLabel}
                <SvgIcon
                  viewBox="0 0 10 6"
                  sx={{ color: "text.secondary", width: 10, height: 6, ml: 1 }}
                >
                  <path
                    fillRule="evenodd"
                    clipRule="evenodd"
                    d="M5 6L10 0H0L5 6Z"
                  />
                </SvgIcon>
              </Button>
            </Box>
          )}
          {!disable_error && (
            <Typography variant="body1" gutterBottom component="h1">
              Error Message
            </Typography>
          )}
          {!disable_error && (
            <FormTextField
              rows={5}
              multiline
              placeholder="Error Message"
              control={control}
              name="error_msg"
              onFocus={() => toggleEditingData(true)}
              onBlur={() => toggleEditingData(false)}
            />
          )}
        </div>
        <Menu
          anchorEl={anchorEl}
          open={Boolean(anchorEl)}
          anchorOrigin={{
            vertical: "bottom",
            horizontal: "center",
          }}
          transformOrigin={{
            vertical: "top",
            horizontal: "center",
          }}
          onClose={handleCloseListItem}
          MenuListProps={{
            "aria-labelledby": "lock-button",
            role: "listbox",
          }}
          sx={{
            "& .MuiPopover-paper": {
              borderRadius: 2,
            },

            "& .MuiList-root": {
              width: "100%",
              maxWidth: 170,
              py: 0,

              "& .MuiMenuItem-root": {
                color: "#000000",
                minHeight: 44,
                fontSize: 14,
                fontWeight: 400,
                lineHeight: "16px",
                py: 1.5,
                px: 2.75,

                "&:hover": {
                  backgroundColor: "#F2F3F7",
                },
              },
            },
          }}
        >
          <TextField
            InputProps={{
              endAdornment: (
                <InputAdornment position="end">
                  <CustomIcon.Search
                    sx={{ color: "#24272C80", width: 16, height: 16 }}
                  />
                </InputAdornment>
              ),
            }}
            inputProps={{
              sx: { height: "auto", p: 0 },
            }}
            onFocus={() => toggleEditingData(true)}
            onBlur={() => toggleEditingData(false)}
            placeholder="Search.."
            autoComplete="off"
            value={fieldsFilter}
            onChange={(event: ChangeEvent<HTMLInputElement>) =>
              setFieldsFilter(event.target.value)
            }
            sx={{
              color: "#24272C80",
              fontSize: 14,
              fontWeight: 400,
              lineHeight: "16px",
              minHeight: 44,
              outline: "none",

              "& .MuiFilledInput-root:not(.MuiInputBase-multiline)": {
                backgroundColor: "#ffffff",
                minHeight: 44,
                py: 1.5,
                px: 2.75,
                outline: "none",

                "& .MuiInputAdornment-root": {
                  ml: 0,
                },

                "& .MuiInputAdornment-root.MuiInputAdornment-positionStart:not(.MuiInputAdornment-hiddenLabel)":
                  {
                    mt: 0,
                  },
              },
            }}
          />
          <Divider />
          <Box maxHeight={130} overflow={"auto"}>
            {filteredFields.length > 0 ? (
              filteredFields.map(({ display_name, field_name }, index) => (
                <MenuItem
                  key={field_name}
                  value={field_name}
                  onClick={() => handleDataFieldSelect(field_name)}
                >
                  {display_name}
                </MenuItem>
              ))
            ) : (
              <MenuItem disabled>No result found.</MenuItem>
            )}
          </Box>
        </Menu>
      </Fragment>
    );
  }
);

export default MessageFields;
