import { Close } from "@mui/icons-material";
import { Card, CardContent, IconButton } from "@mui/material";
import React, { FC } from "react";
import classes from "./StandardCard.module.css";

interface StandardCardProps {
  closeCard?: () => void;
  enableClose?: boolean;
  cardTitle?: string | "Untitled";
  style?: React.CSSProperties;
  children?: React.ReactNode | React.ReactNode[];
}

interface CardHeaderProps extends StandardCardProps {}

const CardHeader: FC<CardHeaderProps> = ({
  cardTitle,
  closeCard,
  enableClose,
}) => {
  return (
    <div className={classes.Header}>
      <h1>{cardTitle || "Card Title"}</h1>

      {enableClose && (
        <div className={classes.Card_Control}>
          <IconButton onClick={closeCard}>
            <Close />
          </IconButton>
        </div>
      )}
    </div>
  );
};

const StandardCard: FC<StandardCardProps> = ({
  cardTitle,
  closeCard,
  enableClose = true,
  style,
  children,
}) => {
  return (
    <Card variant="outlined" className={classes.ItemCard} style={style}>
      <CardContent>
        {cardTitle && (
          <CardHeader
            closeCard={closeCard}
            cardTitle={cardTitle}
            enableClose={enableClose}
          />
        )}

        {children}
      </CardContent>
    </Card>
  );
};

export default StandardCard;
